/**
 * Verify 验证码组件
 * @description 分发验证码使用
 * */
import VerifyCode from './Verify/VerifyCode'
import VerifySlide from './Verify/VerifySlide'
import VerifyPoints from './Verify/VerifyPoints'

export default {
    name: 'Vue2Verify',
    props: {
        // 双语化
        locale: {
            require: false,
            type: String,
            default() {
                // 默认语言不输入为浏览器语言
                if (navigator.language) {
                    var language = navigator.language;
                }
                else {
                    var language = navigator.browserLanguage;
                }
                return language
            }
        },
        type: {
            type: String | Number,
            require: false,
            default: 'picture'
        },
        figure: {
            type: Number
        },
        arith: {
            type: Number
        },
        width: {
            type: String
        },
        height: {
            type: String
        },
        fontSize: {
            type: String
        },
        codeLength: {
            type: Number
        },
        mode: {
            type: String
        },
        vOffset: {
            type: Number
        },
        vSpace: {
            type: Number
        },
        explain: {
            type: String
        },
        imgUrl: {
            type: String
        },
        imgName: {
            type: Array
        },
        imgSize: {
            type: Object
        },
        blockSize: {
            type: Object
        },
        barSize: {
            type: Object
        },
        //默认的文字数量
        defaultNum: {
            type: Number
        },
        //校对的文字数量
        checkNum: {
            type: Number
        }
    },
    data() {
        return {
            // 内部类型
            verifyType: undefined,
            // 所用组件类型
            componentType: undefined
        }
    },
    methods: {
        /**
         * i18n
         * @description 兼容vue-i18n 调用$t来转换ok
         * @param {String} text-被转换的目标
         * @return {String} i18n的结果
         * */
        i18n(text) {
            if (this.$t) {
                return this.$t(text)
            } else {
                // 兼容不存在的语言
                let i18n = this.$options.i18n.messages[this.locale] || this.$options.i18n.messages['en-US']
                return i18n[text]
            }
        },
        /**
         * checkCode
         * @description 判断验证码
         * */
        checkCode() {
            if (this.instance.checkCode) {
                this.instance.checkCode()
            }
        },
        /**
         * refresh
         * @description 刷新
         * */
        refresh() {
            if (this.instance.refresh) {
                this.instance.refresh()
            }
        }

    },
    computed: {
        instance() {
            return this.$refs.instance || {}
        }
    },
    watch: {
        type: {
            immediate: true,
            handler(type) {
                switch (type.toString()) {
                    case 'picture':
                        this.verifyType = '1'
                        this.componentType = 'VerifyCode'
                        break
                    case '1':
                        this.verifyType = '1'
                        this.componentType = 'VerifyCode'
                        break
                    case 'compute':
                        this.verifyType = '2'
                        this.componentType = 'VerifyCode'
                        break
                    case '2':
                        this.verifyType = '2'
                        this.componentType = 'VerifyCode'
                        break
                    case 'slide':
                        this.verifyType = '1'
                        this.componentType = 'VerifySlide'
                        break
                    case '3':
                        this.verifyType = '1'
                        this.componentType = 'VerifySlide'
                        break
                    case 'puzzle':
                        this.verifyType = '2'
                        this.componentType = 'VerifySlide'
                        break
                    case '4':
                        this.verifyType = '2'
                        this.componentType = 'VerifySlide'
                        break
                    case 'pick':
                        this.verifyType = ''
                        this.componentType = 'VerifyPoints'
                        break
                    case '5':
                        this.verifyType = ''
                        this.componentType = 'VerifyPoints'
                        break
                    default:
                        this.verifyType = undefined
                        this.componentType = undefined
                        console.error('Unsupported Type:' + type)
                }
            }
        }
    },
    components: {
        VerifyCode,
        VerifySlide,
        VerifyPoints
    },
    i18n: {
        messages: {
            'en-US': {
                ok: 'ok'
            },
            'zh-CN': {
                ok: '确定'
            }
        }
    }
}



