import $ from "jquery";

export default {
  data() {
    return {
        tinfo:{},
        list:[],
    };
  },
  components: {},
  mounted() {
    let that = this;
    mui.back = function() {
      that.$router.push({
        name: "mine"
      });
      error;
    };
    that
      .$http({
        url: "/user/share_mining",
        method: "post",
        data: {
            token: that.$store.state.user_info.token,
        }
      })
      .then(function(res) {
        if (res.data.code == 1) {
            that.tinfo = res.data.data.user_info;
            that.list = res.data.data.share;
        } else {
          that.$vux.toast.show({
            text: res.data.msg,
            type: "text",
            position: "middle",
            time: 1200
          });
        }
      });
  },
  activated(){
      let that = this;
      
  },
  methods: {}
};



