
import Qs from "qs";
import XDialog from 'vux/src/components/x-dialog/index.vue'

export default {
data() {
return {
show: false,
list: [],
code: "",
email: ""
};
},
components: {
XDialog
},
mounted() {
let that = this;
mui.back = function() {
that.$router.back();
error;
};
},
activated() {
let that = this;
that
.$http({
url: "/user/user_bank",
method: "post",
data: {
token: that.$store.state.user_info.token
}
})
.then(function(res) {
if (res.data.code == 1) {
that.email = res.data.data.email;
if (res.data.data.list.length) {
for (let v of res.data.data.list) {
let str = "",
arr = [];
let n = parseInt(v.bank_card.length / 4);
let nn=n;
if(v.bank_card.length%4==0){
nn=n-1;
}
for (let i = 0; i < nn; i++) {
arr.push("****&nbsp;&nbsp;&nbsp;");
}
arr.push(v.bank_card.substr(nn*4));
v.bank_card1 = arr.join("");
}
that.list = res.data.data.list;
}
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
},
methods: {
back() {
this.$router.back();
},
deleteCard(id, i, type) {
let that = this;
if (type == 1) {
that.$vux.toast.show({
text: that.$t("tip.card.t1"),
type: "text",
position: "middle",
time: 1200
});
return false;
}
that.$vux.confirm.show({
title: that.$t("confirm.card.title.t1"),
confirmText: that.$t("cfm"),
cancelText: that.$t("cel"),
content: that.$t("confirm.card.con.c1"),
onShow() {},
onHide() {},
onCancel() {},
onConfirm() {
that
.$http({
url: "/user/del_user_bank",
method: "post",
data: {
token: that.$store.state.user_info.token,
id: id
}
})
.then(function(res) {
if (res.data.code == 1) {
that.list.splice(i, 1);
that.$vux.toast.show({
text: res.data.msg,
type: "success",
position: "middle",
time: 1200
});
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
}
});
},
addCard() {
let that = this;
that.$vux.confirm.show({
title: that.$t("confirm.card.title.t2"),
confirmText: that.$t("cfm"),
cancelText: that.$t("cel"),
content: that.$t("confirm.card.con.c2"),
onShow() {},
onHide() {},
onCancel() {},
onConfirm() {
that.$vux.loading.show({
text: ""
});
that
.$http({
url: "/user/user_send_email",
method: "post",
data: {
token: that.$store.state.user_info.token
}
})
.then(function(res) {
that.$vux.loading.hide();
if (res.data.code == 1) {
that.$vux.toast.show({
text: res.data.msg,
type: "success",
position: "middle",
time: 1200
});
that.code = res.data.data.code;
that.show = true;
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
}
});
},
setDefault(id, i) {
let that = this;
that.$vux.loading.show({
text: ""
});
that
.$http({
url: "/user/default_bank",
method: "post",
data: {
token: that.$store.state.user_info.token,
id: id
}
})
.then(function(res) {
that.$vux.loading.hide();
if (res.data.code == 1) {
that.$vux.toast.show({
text: res.data.msg,
type: "success",
position: "middle",
time: 1200
});
for(let x in that.list){
if(x==i){
that.list[i].default = 1;
}else{
that.list[x].default = 0;
}
}
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
},
sub() {
let that = this;
if (that.code == "") {
that.$vux.toast.show({
text: that.$t("codedialog.c3"),
type: "cancel",
position: "middle",
time: 1200
});
return false;
}
that
.$http({
url: "/user/check_email_code",
method: "post",
data: {
token: that.$store.state.user_info.token,
code: that.code
}
})
.then(function(res) {
if (res.data.code == 1) {
that.$router.push({
name: "addcard"
});
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
}
}
};



