
import { PullRefresh } from "vant";
import LoadMore from 'vux/src/components/load-more/index.vue'


export default {
data() {
return {
list: [],
page: 1,
load: true,
none: false,
lif: true,
nif: false,
loading: false,
isLoading: false
};
},
components: {
LoadMore,
"van-pull-refresh": PullRefresh
},
mounted() {
let that = this;
mui.back = function() {
that.$router.back();
error;
};
that.getlist();
},
methods: {
back() {
let that = this;
that.$router.back();
},
onRefresh() {
let that = this;
that.isLoading = true;
that.nif = false;
that.page = 1;
that.list = [];
that.getlist(0);
},
loadMore() {
let that = this;
that.lif = true;
that.page++;
that.getlist(1);
},
getlist(type) {
let that = this;
if(type){
that.lif = true;
}
that
.$http({
url: "/news/NewsListPage",
method: "post",
data: {
token: that.$store.state.user_info.token,
p: that.page
}
})
.then(function(res) {
that.isLoading = false;
that.lif = false;
if (res.data.code == 1) {
if (res.data.data.list != "") {
that.list = that.list.concat(res.data.data.list);
} else {
that.loading = true;
that.nif = true;
}
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
},
toDetail(id) {
this.$router.push({
name: "noticedetail",
params: {
i: id
}
});
}
}
};



