import $ from "jquery";
export default {
  data() {
    return {
      title: "",
      u_email:"",
      u_phone: "",
      p_type: "",
      u_code: "",
      pwd: "",
      btn_msg: this.$t('code'),
      list: []
    };
  },
  components: {},
  mounted() {
    let that = this;
    mui.back = function() {
      that.$router.back();
      error;
    };
    let ll = "";
    if (that.$store.state.lang == "cn") {
      ll = 1;
    } else if (that.$store.state.lang == "ct") {
      ll == 2;
    } else {
      ll = 3;
    }
    that
      .$http({
        url: "/phone/country_pre",
        method: "post",
        data: {
          language: ll
        }
      })
      .then(function(res) {
        if (res.data.code == 1) {
          that.list = res.data.data;
          that.p_type =
            res.data.data[0].ph_pre_id + "," + res.data.data[0].pre_code;
        } else {
          that.$vux.toast.show({
            text: res.data.msg,
            type: "text",
            position: "middle",
            time: 1200
          });
        }
      });
  },
  methods: {
    back() {
      this.$router.back();
    },
    eyepwd(e) {
      $(e.target)
        .toggleClass("icon-yanjingbi")
        .toggleClass("icon-yanjing");
      if (
        $(e.target)
          .parents("li")
          .find("input")
          .attr("type") == "password"
      ) {
        $(e.target)
          .parents("li")
          .find("input")
          .attr("type", "text");
      } else {
        $(e.target)
          .parents("li")
          .find("input")
          .attr("type", "password");
      }
    },
    getCode() {
      let that = this;
      let reg = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/;
      if (reg.test(that.u_phone)) {
        if (that.btn_msg == that.$t('code')) {
          that.btn_msg = "60"+that.$t('second');
          window.t = setInterval(function() {
            that.time--;
            that.btn_msg = that.time + that.$t('second');
            if (that.time <= 0) {
              that.btn_msg = that.$t('code');
              clearInterval(window.t);
              that.time = 60;
            }
          }, 1000);
          that
            .$http({
              url: "/user/new_phone_code",
              method: "post",
              data: {
                token: that.$store.state.user_info.token,
                new_phone: that.u_phone
              }
            })
            .then(function(res) {
              if (res.data.code == 1) {
                that.$vux.toast.show({
                  text: res.data.msg,
                  type: "success",
                  position: "middle",
                  time: 1500
                });
                that.u_code = res.data.data;
              } else {
                that.$vux.toast.show({
                  text: res.data.msg,
                  type: "text",
                  position: "middle",
                  time: 1200
                });
                that.btn_msg = that.$t('code');
                clearInterval(window.t);
                that.time = 60;
              }
            });
        }
      } else {
        that.$vux.toast.show({
          text: that.$t('tip.phone_error'),
          type: "cancel",
          position: "middle",
          time: 1200
        });
      }
    },
    sub() {
      let that = this;
      if (that.u_email&&that.pwd&&that.u_phone&&that.u_code) {
        that.$vux.loading.show({
          text: ""
        });
        that
            .$http({
              url: "/user/change_phone_email",
              method: "post",
              data: {
                token: that.$store.state.user_info.token,
                email: that.u_email,
                payment_password: that.pwd,
                new_phone:that.u_phone,
                new_phone_code: that.u_code,
                pre_code_id:that.p_type.split(",")[0],
                pre_code:that.p_type.split(",")[1],
              }
            })
            .then(function(res) {
              that.$vux.loading.hide();
              if (res.data.code == 1) {
                that.$vux.toast.show({
                  text: res.data.msg,
                  type: "success",
                  position: "middle",
                  time: 1500
                });
                that.u_phone="";
                that.u_email="";
                that.u_code="";
                that.pwd="";
              } else {
                that.$vux.toast.show({
                  text: res.data.msg,
                  type: "cancel",
                  position: "middle",
                  time: 1200
                });
              }
            });
      }else{
        that.$vux.toast.show({
          text: that.$t('tip.full_tip'),
          type: "cancel",
          position: "middle",
          time: 1200
        });
      }
    }
  }
};



