import $ from "jquery";
export default {
  data() {
    return {
        info:{},
    };
  },
  components: {
    
  },
  mounted() {
    let that = this;
    mui.back = function() {
      that.$router.back();
      error;
    };
    that
      .$http({
        url: "/Transaction/view_details",
        method: "post",
        data: {
          token: that.$store.state.user_info.token,
          trade_id: that.$route.params.id
        }
      })
      .then(function(res) {
        if (res.data.code == 1) {
          that.info = res.data.data;
          console.log(that.info);
        } else {
          that.$vux.toast.show({
            text: res.data.msg,
            type: "text",
            position: "middle",
            time: 1200
          });
        }
      });
  },
  methods: {
    back() {
      this.$router.back();
    },
    // 图片点击放大
    imgClick(){
      $(".img-module").fadeIn(100);
    },
    // 图片点击隐藏
    imgHide(){
      $(".img-module").fadeOut(100);
    }
  }
};



