
import $ from "jquery";
import XInput from 'vux/src/components/x-input/index.vue'

export default {
data() {
return {
ads: "",
w_ads: "",
t_num: "",
t_other: ""
};
},
components: {
XInput
},
mounted() {
let that = this;
mui.back = function() {
that.$router.back();
error;
};
that
.$http({
url: "/wallet/receivablesPage",
method: "post",
data: {
token: that.$store.state.user_info.token,
id: 1
}
})
.then(function(res) {
if (res.data.code == 1) {
that.ads = res.data.data;
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
},
methods: {
back() {
this.$router.back();
},
num(e) {
$(e.target).val(
$(e.target)
.val()
.match(/\d+\.?\d{0,6}/)
);
this.t_num = $(e.target).val();
},
onCopy(e) {
this.$vux.toast.show({
text: this.$t("tip.copy_suc"),
type: "success",
position: "middle",
time: 1200
});
},

onError(e) {
this.$vux.toast.show({
text: this.$t("tip.copy_error"),
type: "cancel",
position: "middle",
time: 1200
});
},
next() {
let that = this;
if (that.w_ads && that.t_num) {
that.$vux.loading.show({
text: ""
});
that
.$http({
url: "/wallet/transferPhone",
method: "post",
data: {
token: that.$store.state.user_info.token
}
})
.then(function(res) {
that.$vux.loading.hide();
if (res.data.code == 1) {
that.$vux.toast.show({
text: res.data.msg,
type: "success",
position: "middle",
time: 1200
});
that.phone_code = res.data.data;
that.show = true;
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
} else {
that.$vux.toast.show({
text: that.$t("tip.full_tip"),
type: "cancel",
position: "middle",
time: 1200
});
}
}
}
};



