
import $ from "jquery";
import XTable from 'vux/src/components/x-table/index.vue'


export default {
data() {
return {
type:0,
usd:"0.00",
baud:"0.00",
list:[]
};
},
components: {
XTable
},
mounted() {
let that = this;
mui.back = function() {
that.$router.push({
name: "mine"
});
error;
};
},
activated(){
let that = this;
that
.$http({
url: "/User/revenue_finance",
method: "post",
data: {
token: that.$store.state.user_info.token,
page:""
}
})
.then(function(res) {
if (res.data.code == 1) {
that.usd = res.data.data.usd;
that.baud = res.data.data.baud;
for(let v of res.data.data.list){
v.time1 = v.create_time.split(" ")[0];
v.time2 = v.create_time.split(" ")[1];
}
that.list = res.data.data.list;
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
},
methods: {
back() {
this.$router.back();
},
navTap(i){
this.type = i;
},
toDetail(){
this.$router.push({
name:"profitaccdetail"
})
},
}
};



