
import $ from "jquery";
import { PullRefresh } from "vant";
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'


export default {
data() {
return {
type: 0,
list: [],
page: 1,
load: true,
none: false,
lif: true,
nif: false,
loading: false,
isLoading: false
};
},
components: {
Tab,
TabItem,
XTable,
LoadMore,
"van-pull-refresh": PullRefresh
},
mounted() {
let that = this;
mui.back = function() {
mui.plusReady(function() {
var main = plus.android.runtimeMainActivity();
main.moveTaskToBack(false);
});
error;
};
this.$nextTick(() => {
if (this.$route.params.type) {
this.type = Number(this.$route.params.type);
} else {
this.type = 0;
}
that.getlist(1);
});
},
methods: {
navTap(i) {
clearInterval(window.t);
this.type = i;
console.log(this.type);
let that = this;
that.loading = false;
that.nif = false;
that.page = 1;
that.list = [];
that.getlist(1);
},
timeArr() {
let that = this;
window.t = setInterval(function() {
for (let i in that.list) {
let h, m, s;
let now = new Date().getTime();
let leftTime = that.list[i].left_times - now / 1000;
if (leftTime > 0) {
h = Math.floor((leftTime / 60 / 60) % 24);
m = Math.floor((leftTime / 60) % 60);
s = Math.floor(leftTime % 60);
if (m < 10) {
m = "0" + m;
}
if (s < 10) {
s = "0" + s;
}
that.list[i].left_time = h + ":" + m + ":" + s;
} else {
h = "00";
m = "00";
s = "00";
that.list[i].left_time = h + ":" + m + ":" + s;
if (that.list[i].operation_type != 3) {
that.list[i].t_type = 1;

}
}
}
}, 1000);
},
cancel(id) {
let that = this;
that.$vux.confirm.show({
title: that.$t("confirm.card.title.t5"),
confirmText: that.$t("cfm"),
cancelText: that.$t("cel"),
content:
"<div style='text-align:left'>" +
that.$t("confirm.card.con.c5") +
"</div>",
onShow() {},
onHide() {},
onCancel() {},
onConfirm() {
that.$vux.loading.show({
text: ""
});
that
.$http({
url: "/Transaction/cancel_trade",
method: "post",
data: {
token: that.$store.state.user_info.token,
trade_id: id
}
})
.then(function(res) {
that.$vux.loading.hide();
if (res.data.code == 1) {
that.$vux.toast.show({
text: res.data.msg,
type: "success",
position: "middle",
time: 1200
});
that.list = [];
that.getlist(1);
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "cancel",
position: "middle",
time: 1200
});
}
});
}
});
},
toPay(id, id1, bool) {
if (bool) {
return false;
}
this.$router.push({
name: "topay",
params: {
id: id,
id1: id1
}
});
},
confirmReceive(id, bool) {
if (bool) {
return false;
}
let that = this;
that.$vux.confirm.show({
title: "确认收款",
confirmText: "确认",
cancelText: "取消",
content: "已收到买家款项,允许订单完成,是否确定?",
onShow() {},
onHide() {},
onCancel() {},
onConfirm() {
that.$vux.loading.show({
text: ""
});
that
.$http({
url: "/Transaction/do_done",
method: "post",
data: {
token: that.$store.state.user_info.token,
order_id: id
}
})
.then(function(res) {
that.$vux.loading.hide();
if (res.data.code == 1) {
that.$vux.toast.show({
text: res.data.msg,
type: "success",
position: "middle",
time: 1200
});
for (let v of that.list) {
if (v.order_id == id) {
v.operation_type = 3;
let tt = new Date().getTime() / 1000;
v.left_times = tt;
break;
}
}
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "cancel",
position: "middle",
time: 1200
});
}
});
}
});
},
onRefresh() {
clearInterval(window.t);
let that = this;
that.isLoading = true;
that.loading = false;
that.nif = false;
that.page = 1;
that.list = [];
that.getlist(0);
},
loadMore() {
let that = this;
that.lif = true;
that.page++;
that.getlist();
},
getlist(i) {
clearInterval(window.t);
let that = this;
if (i) {
that.lif = true;
}
let tt;
if (that.type == 0) {
tt = 2;
} else if (that.type == 1) {
tt = 4;
} else if (that.type == 2) {
tt = 1;
} else {
tt = 3;
}
that
.$http({
url: "/Transaction/trade_list",
method: "post",
data: {
token: that.$store.state.user_info.token,
cur_id: that.$store.state.active_market.id,
page: that.page,
type: tt
}
})
.then(function(res) {
that.lif = false;
that.isLoading = false;
if (res.data.code == 1) {
console.log(res.data);
var arr = [];
if (that.type == 1 || that.type == 3) {
that.timeArr();
}
if (res.data.data.list.length) {
if (that.type == 0 || that.type == 2) {
for (let v of res.data.data.list) {
v.time1 = v.start_date.toString().split(" ")[0];
v.time2 = v.start_date.toString().split(" ")[1];

}
}


that.list = res.data.data.list;
} else {
that.nif = true;
that.loading = true;
}
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
},

cancelJubao(orderId) {
let that = this;
that.$vux.confirm.show({
title: "取消举报",
confirmText: "确认",
cancelText: "取消",
content: "确认取消举报？",
onShow() {},
onHide() {},
onCancel() {},
onConfirm() {
that.$vux.loading.show({
text: ""
});
that
.$http({
url: "/Transaction/report_revoke",
method: "post",
data: {
token: that.$store.state.user_info.token,
order_id: orderId
}
})
.then(function(res) {
that.$vux.loading.hide();
if (res.data.code == 1) {
that.getlist(1);
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "cancel",
position: "middle",
time: 1200
});
}
});
}
});
},

goJubao(orderId) {
let that = this;
that.$vux.confirm.show({
title: "举报",
confirmText: "确认",
cancelText: "取消",
content: "举报后请尽快到联系客服的交易问题专区提交资料，客服会在举报24小时后作出判定,确认举报?",
onShow() {},
onHide() {},
onCancel() {},
onConfirm() {

that
.$http({
url: "/Transaction/report_order",
method: "post",
data: {
token: that.$store.state.user_info.token,
order_id: orderId
}
})
.then(function(res) {
that.$vux.loading.hide();
if (res.data.code == 1) {









that.getlist(1);
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "cancel",
position: "middle",
time: 1200
});
}
});
}
});
}
}
};



