import $ from "jquery";
export default {
  data() {
    return {
      title: "",
      name: "",
      acc: ""
    };
  },
  components: {},
  mounted() {
    let that = this;
    mui.back = function() {
      that.$router.back();
      error;
    };
    if (that.$route.params.type == 0) {
      that.title = that.$t('alipay.title.t1');
    } else {
      that.title = that.$t('alipay.title.t2');
      that.name = sessionStorage.getItem("alipayname");
      that.acc = sessionStorage.getItem("alipayacc");
    }
  },
  methods: {
    back() {
      this.$router.back();
    },
    sub() {
      let that = this;
      if (that.name && that.acc) {
        that.$vux.loading.show({
          text: ""
        });
        that
          .$http({
            url: "/user/add_alipay",
            method: "post",
            data: {
              token: that.$store.state.user_info.token,
              real_name: that.name,
              alipay_account: that.acc
            }
          })
          .then(function(res) {
            that.$vux.loading.hide();
            if (res.data.code == 1) {
              that.$vux.toast.show({
                text: res.data.msg,
                type: "success",
                position: "middle",
                time: 1200
              });
              that.$router.back();
            } else {
              that.$vux.toast.show({
                text: res.data.msg,
                type: "text",
                position: "middle",
                time: 1200
              });
            }
          });
      } else {
        that.$vux.toast.show({
          text: that.$t('tip.full_tip'),
          type: "cancel",
          position: "middle",
          time: 1200
        });
      }
    }
  }
};



