import $ from "jquery";
export default {
  data() {
    return {
      u_name: ""
    };
  },
  components: {},
  mounted() {
    let that = this;
    mui.back = function() {
      that.$router.back();
      error;
    };
  },
  methods: {
    back() {
      this.$router.back();
    },
    sub() {
      let that = this;
      if (that.u_name == "") {
        that.$vux.toast.show({
          text: that.$t('uname.p1'),
          type: "cancel",
          position: "middle",
          time: 1200
        });
        return false;
      }
      that.$vux.loading.show({
          text: ""
        });
      that
        .$http({
          url: "/user/change_user_info",
          method: "post",
          data: {
            token: that.$store.state.user_info.token,
            type: "",
            profile_icon: "",
            nickname: that.u_name
          }
        })
        .then(function(res) {
          that.$vux.loading.hide();
          if (res.data.code == 1) {
            that.$vux.toast.show({
              text: res.data.msg,
              type: "success",
              position: "middle",
              time: 1200
            });
            that.$router.back();
          } else {
            that.$vux.toast.show({
              text: res.data.msg,
              type: "cancel",
              position: "middle",
              time: 1200
            });
          }
        });
    }
  }
};



