
import $ from "jquery";
import Qrcode from 'vux/src/components/qrcode/index.vue'

export default {
data() {
return {
info:{},
ads: ""
};
},
components: {
Qrcode
},
mounted() {
let that = this;
mui.back = function() {
that.$router.push({
name: "mine"
});
error;
};
that
.$http({
url: "/user/invitation",
method: "post",
data: {
token: that.$store.state.user_info.token
}
})
.then(function(res) {
if (res.data.code == 1) {
that.info = res.data.data;
that.ads = res.data.data.invitation_web+"/#/register/code="+res.data.data.inv_code;
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
},
methods: {
onCopy(e) {
this.$vux.toast.show({
text: this.$t('tip.copy_suc'),
type: "success",
position: "middle",
time: 1200
});
},

onError(e) {
this.$vux.toast.show({
text: this.$t('tip.copy_fail'),
type: "cancel",
position: "middle",
time: 1200
});
}
}
};



