
import $ from "jquery";
import XInput from 'vux/src/components/x-input/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import debounce from 'vux/src/tools/debounce/index.js'

export default {
data() {
return {
info: {},
w_ads: "",
t_num: "",
t_other: "",
show: false,
show1: false,
rate: "",
fee: "0.00",
calc: false,
phone_code: "",
email_code: "",
ifEmail: false
};
},
components: {
XInput,
XDialog,
InlineLoading
},
created() {
this.num = debounce(this.fnHandle, 1000);
},
mounted() {
let that = this;
mui.back = function() {
that.$router.back();
error;
};
},
activated() {
let that = this;
that.info = {};
that.w_ads = "";
that.t_num = "";
that.fee = "0.00";
that.t_other = "";
that
.$http({
url: "/wallet/transferPage",
method: "post",
data: {
token: that.$store.state.user_info.token,
id: 1
}
})
.then(function(res) {
if (res.data.code == 1) {
that.info = res.data.data;
that.rate = Number(res.data.data.fee.split("%")[0]);
that.ifEmail = res.data.data.email_verify;
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
},
methods: {
back() {
this.$router.back();
},
fnHandle(e) {
let that = this;
if (!parseFloat(that.t_num) || that.t_num.split(".").length > 2) {
that.$vux.toast.show({
text: that.$t("tip.num_error"),
type: "text",
position: "middle",
time: 1000
});
that.fee = "0.00";
return false;
}
that.calc = true;
that.fee = parseFloat((that.rate / 100) * that.t_num).toFixed(2);
that.calc = false;
},
clear() {
this.fee = "0.00";
},
next() {
let that = this;
if (that.w_ads && that.t_num) {
that.$vux.loading.show({
text: ""
});
that
.$http({
url: "/wallet/transferPhone",
method: "post",
data: {
token: that.$store.state.user_info.token
}
})
.then(function(res) {
that.$vux.loading.hide();
if (res.data.code == 1) {
that.$vux.toast.show({
text: res.data.msg,
type: "success",
position: "middle",
time: 1200
});
that.phone_code = res.data.data;
that.show = true;
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
} else {
that.$vux.toast.show({
text: that.$t("tip.full_tip"),
type: "cancel",
position: "middle",
time: 1200
});
}
},
phoneSub() {
let that = this;
that.$vux.loading.show({
text: ""
});
that
.$http({
url: "/wallet/checkPhone",
method: "post",
data: {
token: that.$store.state.user_info.token,
code: that.phone_code
}
})
.then(function(res) {
that.phone_code = "";
that.$vux.loading.hide();
if (res.data.code == 1) {
that.show = false;
if (that.ifEmail) {
that
.$http({
url: "/wallet/transferEmail",
method: "post",
data: {
token: that.$store.state.user_info.token
}
})
.then(function(res) {
that.$vux.loading.hide();
if (res.data.code == 1) {
that.show = false;
that.$vux.toast.show({
text: res.data.msg,
type: "success",
position: "middle",
time: 1200
});
that.email_code = res.data.data;
that.show1 = true;
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
} else {
that.transfer();
}
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
},
emailSub() {
let that = this;
that.$vux.loading.show({
text: ""
});
that
.$http({
url: "/wallet/checkEmail",
method: "post",
data: {
token: that.$store.state.user_info.token,
code: that.email_code
}
})
.then(function(res) {
that.email_code = "";
that.$vux.loading.hide();
if (res.data.code == 1) {
that.email_code = "";
that.show1 = false;
that.transfer();
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
},
transfer() {
let that = this;
that.$vux.loading.show({
text: ""
});
that
.$http({
url: "/wallet/transferActive",
method: "post",
data: {
token: that.$store.state.user_info.token,
to_address: that.w_ads,
number: that.t_num,
remake: that.t_other,
id: 1
}
})
.then(function(res) {
that.$vux.loading.hide();
if (res.data.code == 1) {
that.$vux.toast.show({
text: res.data.msg,
type: "success",
position: "middle",
time: 1200
});
that.w_ads = "";
that.t_num = "";
that.fee = "0.00";
that.t_other = "";
that.show = false;
that.show1 = false;
that.phone_code = "";
that.email_code = "";
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "cancel",
position: "middle",
time: 1200
});
}
});
}
}
};



