import Qs from "qs";
export default {
  data() {
    return {
      name: "",
      bank: "0",
      card: "",
      ads: "",
      list: []
    };
  },
  components: {},
  mounted() {
    let that = this;
    mui.back = function() {
      that.$router.back();
      error;
    };
    that
      .$http({
        url: "/bank/all_bank",
        method: "post",
        data: {}
      })
      .then(function(res) {
        if (res.data.code == 1) {
          that.list = res.data.data;
        } else {
          that.$vux.toast.show({
            text: res.data.msg,
            type: "text",
            position: "middle",
            time: 1200
          });
        }
      });
  },
  methods: {
    back() {
      this.$router.back();
    },
    sub() {
      let that = this;

      if (that.name && that.bank && that.card && that.ads) {
        that.$vux.loading.show({
          text: ""
        });
        that
          .$http({
            url: "/user/add_bank",
            method: "post",
            data: {
              token: that.$store.state.user_info.token,
              name: that.name,
              bid: that.bank,
              bank_card: that.card,
              bank_add: that.ads
            }
          })
          .then(function(res) {
            that.$vux.loading.hide();
            if (res.data.code == 1) {
              that.$vux.toast.show({
                text: res.data.msg,
                type: "success",
                position: "middle",
                time: 1200
              });
              that.$router.back();
            } else {
              that.$vux.toast.show({
                text: res.data.msg,
                type: "text",
                position: "middle",
                time: 1200
              });
            }
          });
      } else {
        that.$vux.toast.show({
          text: that.$t("tip.full_tip"),
          type: "cancel",
          position: "middle",
          time: 1200
        });
      }
    }
  }
};



