
import $ from "jquery";
import { Switch } from "vant";
import XDialog from 'vux/src/components/x-dialog/index.vue'

export default {
data() {
return {
info: "",
type: 1,
show: false,
value1: false,
value2: true,
value3: true,
active: "",
i: "",
email_code: ""
};
},
components: {
XDialog,
"van-switch": Switch
},
mounted() {
let that = this;
mui.back = function() {
that.$router.back();
error;
};
},
activated() {
let that = this;
that
.$http({
url: "/user/come_in_sec",
method: "post",
data: {
token: that.$store.state.user_info.token
}
})
.then(function(res) {
if (res.data.code == 1) {
that.info = res.data.msg;
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
that
.$http({
url: "/user/user_config",
method: "post",
data: {
token: that.$store.state.user_info.token
}
})
.then(function(res) {
if (res.data.code == 1) {
that.value1 = Boolean(res.data.data.login_email);
that.value2 = Boolean(res.data.data.transfer);
that.value3 = Boolean(res.data.data.change_pay_pwd);
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
},
methods: {
back() {
this.$router.back();
},
toPhone(o) {
if (o == null) {
this.$router.push({
name: "changephone"
});
} else {
this.$router.push({
name: "changephone1"
});
}
},
toEmail() {
this.$router.push({
name: "changeemail"
});
},
toPwd() {
this.$router.push({
name: "changelpwd"
});
},
toPwd1() {
let that = this;
this.type = 2;
if (this.value3) {
that
.$http({
url: "/user/user_send_email",
method: "post",
data: {
token: that.$store.state.user_info.token
}
})
.then(function(res) {
if (res.data.code == 1) {
that.$vux.toast.show({
text: res.data.msg,
type: "success",
position: "middle",
time: 1200
});
that.email_code = res.data.data.code;
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
this.show = true;
} else {
this.$router.push({
name: "changetpwd"
});
}
},
onInput(checked) {
let that = this;
that.type = 1;
that.$vux.confirm.show({
title: that.$t('confirm.card.title.t6'),
confirmText: that.$t('cfm'),
cancelText: that.$t('cel'),
content: that.$t('confirm.card.con.c6'),
onShow() {},
onHide() {},
onCancel() {},
onConfirm() {
that.value1 = checked;
that
.$http({
url: "/user/change_config",
method: "post",
data: {
token: that.$store.state.user_info.token,
type: 1
}
})
.then(function(res) {
if (res.data.code == 1) {
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
}
});
},
onInput1(checked) {
if (this.info.email == "") {
this.$vux.toast.show({
text: that.$t('tip.email_none'),
type: "text",
position: "middle",
time: 1200
});
return false;
}
let that = this;
that.type = 1;
that.$vux.confirm.show({
title: that.$t('confirm.card.title.t6'),
confirmText: that.$t('cfm'),
cancelText: that.$t('cel'),
content: that.$t('confirm.card.con.c6'),
onShow() {},
onHide() {},
onCancel() {},
onConfirm() {
that
.$http({
url: "/user/user_send_email",
method: "post",
data: {
token: that.$store.state.user_info.token
}
})
.then(function(res) {
if (res.data.code == 1) {
that.$vux.toast.show({
text: res.data.msg,
type: "success",
position: "middle",
time: 1500
});
that.email_code = res.data.data.code;
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
that.i = 1;
that.active = checked;
that.show = true;
}
});
},
onInput2(checked) {
if (this.info.email == "") {
this.$vux.toast.show({
text: that.$t('tip.email_none'),
type: "text",
position: "middle",
time: 1200
});
return false;
}
let that = this;
that.$vux.confirm.show({
title: that.$t('confirm.card.title.t6'),
confirmText: that.$t('cfm'),
cancelText: that.$t('cel'),
content: that.$t('confirm.card.con.c6'),
onShow() {},
onHide() {},
onCancel() {},
onConfirm() {
that
.$http({
url: "/user/user_send_email",
method: "post",
data: {
token: that.$store.state.user_info.token
}
})
.then(function(res) {
if (res.data.code == 1) {
that.$vux.toast.show({
text: res.data.msg,
type: "success",
position: "middle",
time: 1200
});
that.email_code = res.data.data.code;
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
that.i = 2;
that.active = checked;
that.show = true;
}
});
},
sub() {
let that = this;
if (that.email_code == "") {
that.$vux.toast.show({
text: that.$t('tip.code_input'),
type: "cancel",
position: "middle",
time: 1200
});
return false;
}
that.$vux.loading.show({
text: ""
});
that
.$http({
url: "/user/check_email_code",
method: "post",
data: {
token: that.$store.state.user_info.token,
code: that.email_code
}
})
.then(function(res) {
that.$vux.loading.hide();
if (res.data.code == 1) {
if (that.type == 1) {
if (that.i == 1) {
that.value2 = that.active;
} else {
that.value3 = that.active;
}
that
.$http({
url: "/user/change_config",
method: "post",
data: {
token: that.$store.state.user_info.token,
type: that.i + 1
}
})
.then(function(res) {
if (res.data.code == 1) {
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
} else {
that.$router.push({
name: "changetpwd"
});
}
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
}
}
};



