import $ from "jquery";

export default {
  data() {
    return {
      info:{},
    };
  },
  components: {},
  mounted() {
    let that = this;
    mui.back = function() {
      that.$router.back();
      error;
    };
  },
  activated(){
    let that = this;
    that
        .$http({
          url: "/wallet/transferDetails",
          method: "post",
          data: {
            type: 1,
            token: that.$store.state.user_info.token,
            id:that.$route.params.id
          }
        })
        .then(function(res) {
          if (res.data.code == 1) {

            that.info = res.data.data;
          } else {
            that.$vux.toast.show({
              text: res.data.msg,
              type: "cancel",
              position: "middle",
              time: 1200
            });
          }
        });
  },
  methods: {
    back() {
      this.$router.back();
    }
  }
};



