import $ from "jquery";
export default {
  data() {
    return {
      title: "",
      pwd: "",
      pwd1: "",
      pwd2: "",
      u_code: "",
      btn_msg: this.$t('code'),
      time:60,
    };
  },
  components: {},
  mounted() {
    let that = this;
    mui.back = function() {
      that.$router.back();
      error;
    };
  },
  methods: {
    back() {
      this.$router.back();
    },
    eyepwd(e) {
      $(e.target)
        .toggleClass("icon-yanjingbi")
        .toggleClass("icon-yanjing");
      if (
        $(e.target)
          .parents("li")
          .find("input")
          .attr("type") == "password"
      ) {
        $(e.target)
          .parents("li")
          .find("input")
          .attr("type", "text");
      } else {
        $(e.target)
          .parents("li")
          .find("input")
          .attr("type", "password");
      }
    },
    getCode() {
      let that = this;
      if (that.btn_msg == that.$t('code')) {
        that.btn_msg = "60"+that.$t('second');
        window.t = setInterval(function() {
          that.time--;
          that.btn_msg = that.time + that.$t('second');
          if (that.time <= 0) {
            that.btn_msg = that.$t('code');
            clearInterval(window.t);
            that.time = 60;
          }
        }, 1000);
        that
          .$http({
            url: "/user/user_send_email",
            method: "post",
            data: {
              token: that.$store.state.user_info.token
            }
          })
          .then(function(res) {
            if (res.data.code == 1) {
              that.$vux.toast.show({
                text: res.data.msg,
                type: "success",
                position: "middle",
                time: 1500
              });
              that.u_code = res.data.data.code;
            } else {
              that.$vux.toast.show({
                text: res.data.msg,
                type: "text",
                position: "middle",
                time: 1200
              });
              that.btn_msg = that.$t('code');
              clearInterval(window.t);
              that.time = 60;
            }
          });
      }
    },
    sub() {
      let that = this;
      if (that.pwd&&that.pwd1&&that.pwd2&&that.u_code) {
        that
          .$http({
            url: "/user/change_pay_password",
            method: "post",
            data: {
              token: that.$store.state.user_info.token,
              old_password:that.pwd,
              new_password:that.pwd1,
              re_new_password:that.pwd2,
              email_code:that.u_code
            }
          })
          .then(function(res) {
            if (res.data.code == 1) {
              that.$vux.toast.show({
                text: res.data.msg,
                type: "success",
                position: "middle",
                time: 1500
              });
              that.pwd="";
              that.pwd1="";
              that.pwd2="";
              that.u_code="";
            } else {
              that.$vux.toast.show({
                text: res.data.msg,
                type: "cancel",
                position: "middle",
                time: 1200
              });
            }
          });
      }else{
        that.$vux.toast.show({
          text: that.$t('tip.full_tip'),
          type: "cancel",
          position: "middle",
          time: 1200
        });
      }
    }
  }
};



