
import axios from "axios";
import $ from "jquery";
import XInput from 'vux/src/components/x-input/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import SIdentify from "../components/icode";
import Verify from "vue2-verify";

export default {
data() {
return {
lang: "",
lang_dlg: false,
show: false,
usercode: "",
identifyCode: "",
identifyCodes: "123456789",
u_phone: "",
u_pwd: "",
height: "",
img_url: "",
img_arr: [""],
dialog: false,
email_code: "",
type: 0
};
},
components: {
XInput,
XDialog,
SIdentify,
Verify
},
beforeCreate() {
function plusReady() {

plus.navigator.setStatusBarBackground("#010101");
}
if (window.plus) {
plusReady();
} else {
document.addEventListener("plusready", plusReady, false);
}
},
created() {
let that = this;
setTimeout(function() {
that.$store.state.imgIf = false;
}, 4000);
},
mounted() {
let that = this;



that.refresh();
that.lang = that.$store.state.lang;
let height =
document.documentElement.clientHeight || document.body.clientHeight;
that.height = height;
$(".wrap").css("min-height", height);
if (window.localStorage.getItem("acc")) {
that.u_phone = window.localStorage.getItem("acc");
}
that.getIcode();
that
.$http({
url: "/Phone/lang",
method: "post",
data: {
lang: that.$store.state.lang
}
})
.then(function(res) {});
},
methods: {
eyepwd(e) {
$(e.target)
.toggleClass("icon-yanjingbi")
.toggleClass("icon-yanjing");
if (
$(e.target)
.parents(".f_r")
.find("input")
.attr("type") == "password"
) {
$(e.target)
.parents(".f_r")
.find("input")
.attr("type", "text");
} else {
$(e.target)
.parents(".f_r")
.find("input")
.attr("type", "password");
}
},
changeLang(l) {
let that = this;
that.setLang(l);
that.lang_dlg = false;
that
.$http({
url: "/Phone/lang",
method: "post",
data: {
lang: that.$store.state.lang
}
})
.then(function(res) {});
},
sucCode(o) {
let that = this;
let ll = "";
if (that.$store.state.lang == "cn") {
ll = 1;
} else if (that.$store.state.lang == "ct") {
ll = 2;
} else {
ll = 3;
}
that
.$http({
url: "/phone/paths",
method: "post",
data: {
token: that.$store.state.user_info.token,
type: 1,
language: ll
}
})
.then(function(res) {
if (res.data.code == 1) {
that.$router.push({
name: "home"
});
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
that
.$http({
url: "/phone/img_code",
method: "post",
data: {}
})
.then(function(res) {
that.img_url = res.data;
});
}
});
},
failCode() {
let that = this;
let ll = "";
if (that.$store.state.lang == "cn") {
ll = 1;
} else if (that.$store.state.lang == "ct") {
ll = 2;
} else {
ll = 3;
}
that
.$http({
url: "/phone/img_code",
method: "post",
data: {}
})
.then(function(res) {
that.img_url = res.data;
});
that
.$http({
url: "/phone/paths",
method: "post",
data: {
token: that.$store.state.user_info.token,
type: 2,
language: ll
}
})
.then(function(res) {});
},
refresh() {
let that = this;
that
.$http({
url: "/phone/img_code",
method: "post",
data: {}
})
.then(function(res) {
that.img_url = res.data;
});
},
focus(v, e) {
this.$store.commit("focus", { e: e });
},
blur(v, e) {
this.$store.commit("blur", { e: e });
},
getIcode() {
this.usercode = "";
this.identifyCode = "";
this.makeCode(this.identifyCodes, 4);
},

randomNum(min, max) {
return Math.floor(Math.random() * (max - min) + min);
},
refreshCode() {
this.identifyCode = "";
this.makeCode(this.identifyCodes, 4);
},
makeCode(o, l) {
for (let i = 0; i < l; i++) {
this.identifyCode += this.identifyCodes[
this.randomNum(0, this.identifyCodes.length)
];
}
},
sub() {
let that = this;
if (that.u_phone && that.u_pwd) {
let reg = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;
if (!reg.test(that.u_phone)) {
that.$vux.toast.show({
text: that.$t("tip.email_error"),
type: "cancel",
position: "middle",
time: 1200
});
return false;
}
that.$vux.loading.show({
text: that.$t("load.logining")
});
let ll = "";
if (that.$store.state.lang == "cn") {
ll = 1;
} else if (that.$store.state.lang == "ct") {
ll = 2;
} else {
ll = 3;
}
that
.$http({
url: "/phone/userLogin",
method: "post",
data: {
email: that.u_phone,
password: that.u_pwd,
language: ll,
email_code: ""
}
})
.then(function(res) {
that.$vux.loading.hide();
if (res.data.code == 1) {
that.$store.state.user_info = res.data.data;
let l;
if (res.data.data.language == 1) {
l = "cn";
} else if (res.data.data.language == 2) {
l = "ct";
} else {
l = "en";
}
that.$store.state.lang = l;
that.setLang(that.lang);
window.sessionStorage.setItem(
"uinfo",
JSON.stringify(res.data.data)
);
window.localStorage.setItem("acc", that.u_phone);
that.dialog = true;

} else if (res.data.code == 2) {
that.show = true;
that
.$http({
url: "/phone/email_code",
method: "post",
data: {
email: that.u_phone
}
})
.then(function(res) {
if (res.data.code == 1) {
that.$vux.toast.show({
text: res.data.msg,
type: "success",
position: "middle",
time: 1500
});
that.email_code = res.data.data;
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "cancel",
position: "middle",
time: 1200
});
}
});
} else {
this.$vux.toast.show({
text: that.$t("tip.full_tip"),
type: "cancel",
position: "middle",
time: 1200
});
}
that.usercode = "";
},
sub1() {
let that = this;
that.$vux.loading.show({
text: that.$t("load.logining")
});
let ll;
if (that.$store.state.lang == "cn") {
ll = 1;
} else if (that.$store.state.lang == "ct") {
ll = 2;
} else {
ll = 3;
}
that
.$http({
url: "/phone/userLogin",
method: "post",
data: {
email: that.u_phone,
password: that.u_pwd,
language: ll,
email_code: that.email_code
}
})
.then(function(res) {
that.$vux.loading.hide();
if (res.data.code == 1) {
that.show = false;
let l;
if (res.data.data.language == 1) {
l = "cn";
} else if (res.data.data.language == 2) {
l = "ct";
} else {
l = "en";
}
that.$store.state.lang = l;
that.setLang(that.lang);
that.$store.state.user_info = res.data.data;
window.sessionStorage.setItem(
"uinfo",
JSON.stringify(res.data.data)
);
window.localStorage.setItem("acc", that.u_phone);
that.dialog = true;

} else {
that.$vux.toast.show({
text: res.data.msg,
type: "cancel",
position: "middle",
time: 1200
});
that.email_code = "";
that.show = false;
}
});
}
}
};



