
import $ from "jquery";
import { Switch } from "vant";
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'

export default {
data() {
return {
type: 1,
value: false,
str: "",
str1: "",
list: "",
auto: false,
show: false
};
},
components: {
XDialog,
XTable,
"van-switch": Switch
},
mounted() {
let that = this;
mui.back = function() {
mui.plusReady(function() {
var main = plus.android.runtimeMainActivity();
main.moveTaskToBack(false);
});
error;
};






















































},
activated() {
let that = this;
that
.$http({
url: "/notice/mining_notice",
method: "post",
data: {
type: 1,
token: that.$store.state.user_info.token
}
})
.then(function(res) {
if (res.data.code == 1) {
that.str = res.data.data;
sessionStorage.setItem("str", res.data.data);
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "cancel",
position: "middle",
time: 1200
});
}
});
that
.$http({
url: "/notice/mining_notice",
method: "post",
data: {
type: 2,
token: that.$store.state.user_info.token
}
})
.then(function(res) {
if (res.data.code == 1) {
that.str1 = res.data.data;
sessionStorage.setItem("str1", res.data.data);
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "cancel",
position: "middle",
time: 1200
});
}
});

that
.$http({
url: "/user/get_auto_withdraw",
method: "post",
data: {
token: that.$store.state.user_info.token
}
})
.then(function(res) {
if (res.data.code == 1) {
that.auto = Boolean(res.data.data.status);

} else {
that.$vux.toast.show({
text: res.data.msg,
type: "cancel",
position: "middle",
time: 1200
});
}
});

that
.$http({
url: "/user/mining",
method: "post",
data: {
token: that.$store.state.user_info.token
}
})
.then(function(res) {
if (res.data.code == 1) {
res.data.data.status = Boolean(res.data.data.status);
that.list = res.data.data;
console.log(that.list);
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "cancel",
position: "middle",
time: 1200
});
}
});
},
methods: {
onInput(checked) {
let that = this;
that.$vux.loading.show({
text: ""
});
that
.$http({
url: "/user/mining_total_switch",
method: "post",
data: {
token: that.$store.state.user_info.token
}
})
.then(function(res) {
that.$vux.loading.hide();
if (res.data.code == 1) {
that.$vux.toast.show({
text: res.data.msg,
type: "success",
position: "middle",
time: 1200
});
that.list.status = checked;
that.list.working = res.data.data;
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "cancel",
position: "middle",
time: 1200
});
}
});
},
toLink() {
this.$router.push({
name: "exploit"
});
},
extract() {
let that = this;
if(that.list.withdraw_status==0){
return false;
}
that.$vux.confirm.show({
title: that.$t("confirm.card.title.t4"),
confirmText: that.$t("cfm"),
cancelText: that.$t("cel"),
content: that.$t("confirm.card.con.c4"),
onShow() {},
onHide() {},
onCancel() {},
onConfirm() {
that.$vux.loading.show({
text: ""
});
that
.$http({
url: "/user/withdraw_mining",
method: "post",
data: {
token: that.$store.state.user_info.token
}
})
.then(function(res) {
that.$vux.loading.hide();
if (res.data.code == 1) {
that.$vux.toast.show({
text: res.data.msg,
type: "success",
position: "middle",
time: 1200
});
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "cancel",
position: "middle",
time: 1200
});
}
});
}
});
},
autoSub() {
let that = this;
if (that.auto == 0) {
that.show = false;
that.$vux.loading.show({
text: ""
});
that
.$http({
url: "/user/change_auto_withdraw",
method: "post",
data: {
token: that.$store.state.user_info.token,
switch: 1
}
})
.then(function(res) {
that.$vux.loading.hide();
if (res.data.code == 1) {
that.$vux.toast.show({
text: res.data.msg,
type: "success",
position: "middle",
time: 1200
});
that.auto = !that.auto;
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "cancel",
position: "middle",
time: 1200
});
}
});
} else {
that.$vux.toast.show({
text: that.$t("tip.open_error"),
type: "text",
position: "middle",
time: 1200
});
}
},
autoClose() {
let that = this;
if (that.auto == 1) {
that.show = false;
that.$vux.loading.show({
text: ""
});
that
.$http({
url: "/user/change_auto_withdraw",
method: "post",
data: {
token: that.$store.state.user_info.token,
switch: 0
}
})
.then(function(res) {
that.$vux.loading.hide();
if (res.data.code == 1) {
that.$vux.toast.show({
text: res.data.msg,
type: "success",
position: "middle",
time: 1200
});
that.auto = !that.auto;
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "cancel",
position: "middle",
time: 1200
});
}
});
} else {
that.$vux.toast.show({
text: that.$t("tip.close_error"),
type: "text",
position: "middle",
time: 1200
});
}
}
}
};



