
import $ from "jquery";
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

export default {
data() {
return {
nid: this.$store.state.nid,
nid1: "0",
dialog: false,
interval: 2500,
imgs: [],
notices: [],
tabIndex: 0,
rules: "",
awdinfo:"",
};
},
components: {
Swiper,
SwiperItem,
XDialog
},
mounted() {
let that = this;
mui.back = function() {
mui.plusReady(function() {
var main = plus.android.runtimeMainActivity();
main.moveTaskToBack(false);
});
error;
};
},
activated(){
let that = this;

that
.$http({
url: "/Phone/homePage",
method: "post",
data: {}
})
.then(function(res) {
if (res.data.code == 1) {
that.awdinfo = res.data.data.wining;
that.notices = res.data.data.news;
that.imgs = res.data.data.banner;
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
that
.$http({
url: "/market/index",
method: "post",
data: {
token: that.$store.state.user_info.token
}
})
.then(function(res) {
if (res.data.code == 1) {
that.$store.state.active_market = res.data.data[0];
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
},
methods: {
toDetail(o) {
this.$router.push({
name: "notices"
});
},
tip() {
let that = this;
that.$vux.alert.show({
title: "",
content: that.$t('none'),
buttonText:  that.$t('cfm'),
onShow() {},
onHide() {}
});
}
}
};



