
import $ from "jquery";
import XDialog from 'vux/src/components/x-dialog/index.vue'


export default {
data() {
return {
lang: "",
lang_dlg: false,
alipay: "",
wechat: "",
bank: "",
paypal: ""
};
},
components: {XDialog},
mounted() {
let that = this;
mui.back = function() {
that.$router.back();
error;
};
},
activated() {
this.lang = this.$store.state.lang;
let that = this;
that
.$http({
url: "/user/get_alipay",
method: "post",
data: {
token: that.$store.state.user_info.token
}
})
.then(function(res) {
if (res.data.code == 1) {
that.alipay = res.data.data.alipay_account;
that.wechat = res.data.data.wechat_img;
that.bank = res.data.data.bank;
that.paypal = res.data.data.paypal;
if (res.data.data.alipay_account) {
sessionStorage.setItem("alipayname", res.data.data.alipay_name);
sessionStorage.setItem("alipayacc", res.data.data.alipay_account);
}
if (res.data.data.wechat_img) {
sessionStorage.setItem("wechatnick", res.data.data.wechat_nick);
sessionStorage.setItem("wechatimg", res.data.data.wechat_img);
}
if (res.data.data.paypal) {
sessionStorage.setItem("paypal", res.data.data.paypal);
}
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
},
methods: {
back() {
this.$router.back();
},
toBook() {
this.$router.push({
name: "book"
});
},
toCard() {
this.$router.push({
name: "bankcard"
});
},
toAlipay() {
let t;
if (this.alipay) {
t = 1;
} else {
t = 0;
}
this.$router.push({
name: "alipay1",
params: {
type: t
}
});
},
toWx() {
let t;
if (this.wechat) {
t = 1;
} else {
t = 0;
}
this.$router.push({
name: "wechat1",
params: {
type: t
}
});
},
toPaypal() {
let t;
if (this.paypal) {
t = 1;
} else {
t = 0;
}
this.$router.push({
name: "paypal",
params: {
type: t
}
});
},
changeLang(l) {
this.setLang(l);
let that = this;
that.$vux.loading.show({
text: ""
});
that
.$http({
url: "/Phone/lang",
method: "post",
data: {
token: that.$store.state.user_info.token,
lang: l
}
})
.then(function(res) {
that.$vux.loading.hide();
if (res.data.code == 1) {
that.lang_dlg = false;
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
}
}
};



