import $ from "jquery";
import Qs from "qs";
export default {
  data() {
    return {
      title: "",
      pwd: "",
      pwd1: "",
      pwd2: ""
    };
  },
  components: {},
  mounted() {
    let that = this;
    mui.back = function() {
      that.$router.back();
      error;
    };
  },
  methods: {
    back() {
      this.$router.back();
    },
    eyepwd(e) {
      $(e.target)
        .toggleClass("icon-yanjingbi")
        .toggleClass("icon-yanjing");
      if (
        $(e.target)
          .parents("li")
          .find("input")
          .attr("type") == "password"
      ) {
        $(e.target)
          .parents("li")
          .find("input")
          .attr("type", "text");
      } else {
        $(e.target)
          .parents("li")
          .find("input")
          .attr("type", "password");
      }
    },
    sub() {
      let that = this;
      if (that.pwd && that.pwd1 && that.pwd2) {
        if (that.pwd1 != that.pwd2) {
          that.$vux.toast.show({
            text: that.$t('tip.pwd_again1'),
            type: "cancel",
            position: "middle",
            time: 1200
          });
          return false;
        }
        that
          .$http({
            url: "/user/change_password",
            method: "post",
            data: {
              token: that.$store.state.user_info.token,
              old_password: that.pwd,
              new_password: that.pwd1,
              re_new_password: that.pwd2
            }
          })
          .then(function(res) {
            if (res.data.code == 1) {
              that.$vux.alert.show({
                title: that.$t('suc'),
                content: that.$t('confirm.c1'),
                buttonText: that.$t('confirm.b1'),
                onShow() {},
                onHide() {
                  that.dialog0 = false;
                  that.$router.push({
                    name: "login"
                  });
                }
              });
              that.pwd = "";
              that.pwd1 = "";
              that.pwd2 = "";
            } else {
              that.$vux.toast.show({
                text: res.data.msg,
                type: "cancel",
                position: "middle",
                time: 1200
              });
            }
          });
      } else {
        that.$vux.toast.show({
          text: that.$t('tip.full_tip'),
          type: "cancel",
          position: "middle",
          time: 1200
        });
      }
    }
  }
};



