import $ from "jquery";

export default {
  data() {
    return {
      uinfo:"",
      info:"",
      ifMsg:0,
    };
  },
  components: {},
  mounted() {
    let that = this;
    mui.back = function() {
      mui.plusReady(function() {
        var main = plus.android.runtimeMainActivity();
        main.moveTaskToBack(false);
      });
      error;
    };
  },
  activated (){
    let that = this;
    that
      .$http({
        url: "/user/user_head_info",
        method: "post",
        data: {
          token: that.$store.state.user_info.token
        }
      })
      .then(function(res) {
        if (res.data.code == 1) {
          that.uinfo = res.data.data;
          that.info = res.data.data.user_info;
        } else {
          that.$vux.toast.show({
            text: res.data.msg,
            type: "text",
            position: "middle",
            time: 1200
          });
        }
      });
      that
      .$http({
        url: "/user/message_list",
        method: "post",
        data: {
          token: that.$store.state.user_info.token,
          visit: 1
        }
      })
      .then(function(res) {
        if (res.data.code == 1) {
          that.ifMsg = res.data.data.length;
        } else {
          that.$vux.toast.show({
            text: res.data.msg,
            type: "text",
            position: "middle",
            time: 1200
          });
        }
      });
  },
  methods: {
    exit() {
      let that = this;
      that.$vux.confirm.show({
        title: that.$t('confirm.card.title.t3'),
        confirmText: that.$t('cfm'),
        cancelText: that.$t('cel'),
        content: that.$t('confirm.card.con.c3'),
        onShow() {},
        onHide() {},
        onCancel() {},
        onConfirm() {
          that.$router.push({
            name: "login"
          });
          sessionStorage.removeItem("uinfo");
        }
      });
    },
    toAccinfo() {
      this.$router.push({
        name: "accinfo"
      });
    },
    toVip() {
      this.$router.push({
        name: "vip"
      });
    },
    toShare() {
      this.$store.state.n1 = 0;
      this.$router.push({
        name: "myshare"
      });
    },
    toSafe() {
      this.$router.push({
        name: "safecenter"
      });
    },
    toSet() {
      this.$router.push({
        name: "syssetting"
      });
    },
    toChat() {
      this.$router.push({
        name: "message"
      });
    },
    toMsg(){
      this.$router.push({
        name: "msg",
        params:{
          type:1
        }
      });
    },
     toBook(){
      this.$router.push({
        name: "book"
      });
    },
    toLink(i) {
      if (i) {
        this.$router.push({
          name: "profitacc"
        });
      } else {
        this.$router.push({
          name: "mainacc"
        });
      }
    },
    toContribute(){
      this.$store.state.n1 = 2;
      this.$router.push({
        name:"mycontribute"
      })
    }
  }
};



