import $ from "jquery";
import echarts from "echarts";
export default {
  data() {
    return {
      name:"",
      today:"",
      times:[],
      yvalues:[],
      img:"",
    };
  },
  components: {},
  mounted() {
    let that = this;
    mui.back = function() {
      that.$router.push({
        name: "mine"
      });
      error;
    };
    // 图表
    that
      .$http({
        url: "/user/get_contribution_his",
        method: "post",
        data: {
          token: that.$store.state.user_info.token
        }
      })
      .then(function(res) {
        that.name = res.data.data.name;
        that.img = res.data.data.head_icon;
        that.today = res.data.data.today;
        that.times = res.data.data.times;
        that.yvalues = res.data.data.values;
        that.drawChart("myChart");
      });
  },
  methods: {
    drawChart(id) {
      var myChart = echarts.init(document.getElementById(id));
      let that = this;
      // 指定图表的配置项和数据
      var option = {
        grid: {
          left: "20%",
          top: "20",
          height: "160",
          width: "76%"
        },
        textStyle: {
          color: "rgba(255,255,255,.6)"
        },
        tooltip: {
            trigger: 'axis',
            axisPointer:{
              type:"line",
              snap:true,
            }
        },
        xAxis: {
          type: "category",
          data: that.times,
          axisLine: {
            onZero: false,
            lineStyle: {
              color: "rgba(255,255,255,.6)"
            }
          },
          // axisTick: {//刻度线
          //   show: false,//去掉刻度线
          // },
        },
        yAxis: {
          type: "value",
          name:"",
          nameLocation:"start",
          nameGap:10,
          nameTextStyle:{
            padding:[-16,100,0,0]
          },
          axisLine: {
            onZero: false,
            lineStyle: {
              color: "rgba(255,255,255,.6)"
            }
          },
          splitLine: {
            show:false,
            lineStyle: {
              type: "solid",
              color: "rgba(255,255,255,.3)"
            }
          },
          axisLabel: {
            formatter: function(value, index) {
              return value.toFixed(2);
            }
          },
          // axisTick: {//刻度线
          //   show: false,//去掉刻度线
          // },
        },
        dataZoom: [
          {
            type: "inside",
            startValue: that.times.length-5>0?that.times.length-5:0,
            endValue: that.times.length-1
          }
        ],
        series: [
          {
            data: that.yvalues,
            type: "bar",
            barMaxWidth:40,
            lineStyle:{
              color:"white"
            },
            itemStyle:{
              color:"white"
            }
          }
        ]
      };

      // 使用刚指定的配置项和数据显示图表。
      myChart.clear(option, true);
      myChart.setOption(option, true);
    }
  }
};



