/*****************计算macd**************macd*/

build_diff(m_short, m_long, data) {
var result = [];
var pre_emashort = 0;
var pre_emalong = 0;
for (var i = 0; i < data.length; i++) {
var ema_short = data[i][2]; 
var ema_long = data[i][2]; 
if (i != 0) {
ema_short =
(1.0 / m_short) * data[i][2] + (1 - 1.0 / m_short) * pre_emashort;
ema_long =
(1.0 / m_long) * data[i][2] + (1 - 1.0 / m_long) * pre_emalong;
}
pre_emashort = ema_short;
pre_emalong = ema_long;
var diff = parseInt((ema_short - ema_long) * 100) / 100;
result.push(diff);
}
return result;
},

build_dea(m, diff) {
var result = [];
var pre_ema_diff = 0;
for (var i = 0; i < diff.length; i++) {
var ema_diff = diff[i];
if (i != 0) {
ema_diff = (1.0 / m) * diff[i] + (1 - 1.0 / m) * pre_ema_diff;
}
pre_ema_diff = ema_diff;
ema_diff = parseInt(ema_diff * 100) / 100;
result.push(ema_diff);
}
return result;
},

build_macd(data, diff, dea) {
var result = [];
for (var i = 0; i < data.length; i++) {
var macd = 2 * (diff[i] - dea[i]);
macd = parseInt(macd * 100) / 100;
result.push(macd);
}
return result;
},
/*****************计算macd***************/
build_all_data(res) {
let that = this;
var line_data = [];
for (var i = 0; i < res.length; i++) {


var time = res[i].time;
var line_arr = [
time,
res[i].open,
res[i].close,
res[i].low,
res[i].high,
res[i].vol
];
line_data.push(line_arr);
}
var diff = that.build_diff(12, 26, line_data);
var dea = that.build_dea(9, diff);
var macd = that.build_macd(line_data, diff, dea);
var all_data = [];
for (var i = 0; i < line_data.length; i++) {

var arr = [
line_data[i][0],
line_data[i][1],
line_data[i][2],
line_data[i][3],
line_data[i][4],
line_data[i][5],
macd[i],
diff[i],
dea[i]
];
all_data.push(arr);
}
return all_data;
},

splitData(rawData) {
var datas = [];
var times = [];
var vols = [];
var macds = [];
var difs = [];
var deas = [];
for (var i = 0; i < rawData.length; i++) {
datas.push(rawData[i]);
vols.push(parseInt(rawData[i][5]));
macds.push(rawData[i][6]);
difs.push(rawData[i][7]);
deas.push(rawData[i][8]);
times.push(rawData[i].splice(0, 1)[0]);
}
return {
datas: datas,
times: times,
vols: vols,
macds: macds,
difs: difs,
deas: deas
};
},


calculateMA(dayCount, data) {
var result = [];

for (var i = 0, len = data.times.length; i < len; i++) {
if (i < dayCount) {
result.push("-");
continue;
}
var sum = 0;
for (var j = 0; j < dayCount; j++) {
sum += parseFloat(data.datas[i - j][1]);
}

result.push((sum / dayCount).toFixed(2));
}
return result;
},

