splitData1(rawData) {
var times = [];
var price = [];
var vols = [];
var datas = [];
for (var i = 0; i < rawData.length; i++) {
datas.push(rawData[i]);
price.push(parseFloat(rawData[i].close));
vols.push(parseInt(rawData[i].vol));
times.push(rawData[i].time.split(" ")[1]);
}
return {
datas: datas,
times: times,
price: price,
vols: vols
};
