
import $ from "jquery";
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'


export default {
data() {
return {
show: true,
list: [],
list1: [],
type: 1
};
},
components: {
Tab,
TabItem
},
mounted() {
let that = this;
mui.back = function() {
that.$router.push({
name: "mine"
});
error;
};
},
activated() {
let that = this;
that.$nextTick(function() {
if(that.$route.params.type==1){
that.type = 1;
that.getList(1);
}else{
that.type = 2;
that.getList(2);
}
});
},
methods: {
back() {
this.$router.push({
name: "mine"
});
},
getList(i){
let that = this;
that.type = i;
if(i==1){
that.show = true;
}else{
that.show = false;
}
that
.$http({
url: "/user/message_list",
method: "post",
data: {
token: that.$store.state.user_info.token,
visit: that.type
}
})
.then(function(res) {
if (res.data.code == 1) {
if(that.type==1){
that.list = res.data.data;
}else{
that.list1 = res.data.data;
}
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
},
toDetail(id,type){
let that = this;
that.$router.push({
name:"msgdetail",
params:{
i:id,
source:type
}
})
}
}
};



