import $ from "jquery";
export default {
  data() {
    return {
      title: "",
      u_phone: "",
      p_type: "",
      u_code: "",
      btn_msg: this.$t('code'),
      time: 60,
      list: []
    };
  },
  components: {},
  mounted() {
    let that = this;
    mui.back = function() {
      that.$router.back();
      error;
    };
    let ll = "";
    if (that.$store.state.lang == "cn") {
      ll = 1;
    } else if (that.$store.state.lang == "ct") {
      ll == 2;
    } else {
      ll = 3;
    }
    that
      .$http({
        url: "/phone/country_pre",
        method: "post",
        data: {
          language: ll
        }
      })
      .then(function(res) {
        if (res.data.code == 1) {
          that.list = res.data.data;
          that.p_type =
            res.data.data[0].ph_pre_id + "," + res.data.data[0].pre_code;
        } else {
          that.$vux.toast.show({
            text: res.data.msg,
            type: "text",
            position: "middle",
            time: 1200
          });
        }
      });
  },
  methods: {
    back() {
      this.$router.back();
    },
    getCode() {
      let that = this;
      let reg = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/;
      if (reg.test(that.u_phone)) {
        if (that.btn_msg == that.$t('code')) {
          that.btn_msg = "60"+that.$t('second');
          window.t = setInterval(function() {
            that.time--;
            that.btn_msg = that.time + that.$t('second');
            if (that.time <= 0) {
              that.btn_msg = that.$t('code');
              clearInterval(window.t);
              that.time = 60;
            }
          }, 1000);
          that
            .$http({
              url: "/user/send_phone_code",
              method: "post",
              data: {
                phone: that.u_phone,
                token: that.$store.state.user_info.token
              }
            })
            .then(function(res) {
              if (res.data.code == 1) {
                that.$vux.toast.show({
                  text: res.data.msg,
                  type: "success",
                  position: "middle",
                  time: 1500
                });
                that.u_code = res.data.data.think.phone_code.code;
              } else {
                that.$vux.toast.show({
                  text: res.data.msg,
                  type: "text",
                  position: "middle",
                  time: 1200
                });
                that.btn_msg = that.$t('code');
                clearInterval(window.t);
                that.time = 60;
              }
            });
        }
      } else {
        that.$vux.toast.show({
          text: that.$t('tip.phone_error'),
          type: "cancel",
          position: "middle",
          time: 1200
        });
      }
    },
    sub() {
      let that = this;
      if (that.u_code == "") {
        that.$vux.toast.show({
          text: that.$t('tip.full_tip'),
          type: "cancel",
          position: "middle",
          time: 1200
        });
        return false;
      }
      that.$vux.loading.show({
        text: ""
      });
      that
        .$http({
          url: "/user/bind_phone",
          method: "post",
          data: {
            phone: that.u_phone,
            token: that.$store.state.user_info.token,
            code: that.u_code,
            pre_code_id: that.p_type.split(",")[0],
            pre_code: that.p_type.split(",")[1]
          }
        })
        .then(function(res) {
          that.$vux.loading.hide();
          if (res.data.code == 1) {
            that.$vux.toast.show({
              text: res.data.msg,
              type: "success",
              position: "middle",
              time: 1500
            });
            that.$router.back();
          } else {
            that.$vux.toast.show({
              text: res.data.msg,
              type: "text",
              position: "middle",
              time: 1200
            });
          }
        });
    }
  }
};



