import $ from "jquery";

export default {
  data() {
    return {
      phr:"请详细描述您的问题",
      qid: 0,
      detail: "",
      data1: "",
      data2: "",
      data3: "",
      data4: "",
      data5: "",
      src1:"",
      src2:"",
      src3:"",
      src4:"",
      src5:"",
      srcs:[],
      urll: [],
      list: [],
    };
  },
  components: {},
  mounted() {
    let that = this;
    mui.back = function() {
      that.$router.push({
        name: "mine"
      });
      error;
    };
    that
      .$http({
        url: "/user/feedbackType",
        method: "post",
        data: {
          token: that.$store.state.user_info.token
        }
      })
      .then(function(res) {
        if (res.data.code == 1) {
          that.list = res.data.data;
          that.qid = res.data.data[0].type;
        } else {
          that.$vux.toast.show({
            text: res.data.msg,
            type: "text",
            position: "middle",
            time: 1200
          });
        }
      });
  },
  methods: {
    //改变问题方式
    change(id){
      let that = this;
      // qid=
      that.qid = id;
      if(that.qid == 2){
        that.phr = "请输入交易订单编号并详细描述您的问题，上传该订单对应的收款信息页面截图、未收到款等有助于客服协助解决问题的证据截图。";
      }else{
        that.phr = "请详细描述您的问题";
      }
    },
    // 上传
    pushImg1: function(e,index) {
      let file = e.target,
        reader = new FileReader(),
        that = this,
        _name,
        _fileName;
      _name = file.value;
      _fileName = _name.substring(_name.lastIndexOf(".") + 1).toLowerCase();
      reader.readAsDataURL(file.files[0]);
      if (file.files[0].size > 10 * 1024 * 1024) {
        that.$vux.toast.show({
          text: that.$t("ups.d"),
          type: "warn",
          position: "middle",
          time: 1500
        });
      } else {
        that.$vux.loading.show({
          text: that.$t('load.uping')
        });
        reader.onload = function() {
          let result = this.result;

          // that.data1 = result;

          var image = new FormData();
          image.append("file", e.target.files[0]);
          image.append("type", "1");
          image.append("token", that.$store.state.user_info.token);
          that
            .$http({
              url: "/user/upload_pic",
              method: "post",
              data: image
            })
            .then(function(res) {
              that.$vux.loading.hide();
              if (res.data.code == 1) {
                if(index == 0){
                  that.data1 = result;
                  that.src1 = res.data.url;
                }else if(index == 1){
                  that.data2 = result;
                  that.src2 = res.data.url;
                }else if(index == 2){
                  that.data3 = result;
                  that.src3 = res.data.url;
                }else if(index == 3){
                  that.data4 = result;
                  that.src4 = res.data.url;
                }else if(index == 4){
                  that.data5 = result;
                  that.src5 = res.data.url;
                }


              } else {
                that.$vux.toast.show({
                  text: res.data.msg,
                  type: "cancel",
                  position: "middle",
                  time: 1200
                });
              }
            });
        };
      }
      // }
    },
    delImg1: function(e,index) {
      if(index == 0){
        this.data1 = "";
        let dom = document.getElementById("handcard1");
        this.src1="";
      }else if(index == 1){
        this.data2 = "";
        let dom = document.getElementById("handcard2");
        this.src2="";
      }else if(index == 2){
        this.data3 = "";
        let dom = document.getElementById("handcard3");
        this.src3="";
      }else if(index == 3){
        this.data4 = "";
        let dom = document.getElementById("handcard4");
        this.src4="";
      }else if(index == 4){
        this.data5 = "";
        let dom = document.getElementById("handcard5");
        this.src5="";
      }
      dom.value = "";
      that.srcs = [];
    },
    sub() {
      let that = this;
      if(that.src1 != ""){
        that.srcs.push(that.src1);
      }
      if(that.src2 != ""){
        that.srcs.push(that.src2);
      }
      if(that.src3 != ""){
        that.srcs.push(that.src3);
      }
      if(that.src4 != ""){
        that.srcs.push(that.src4);
      }
      if(that.src5 != ""){
        that.srcs.push(that.src5);
      }
      var srcs = (that.srcs).toString();
      if (that.detail) {
        that.$vux.loading.show({
          text: ""
        });
        that
          .$http({
            url: "/user/feedback",
            method: "post",
            data: {
              token: that.$store.state.user_info.token,
              type: that.qid,
              img_url: srcs,
              content: that.detail
            }
          })
          .then(function(res) {
            that.$vux.loading.hide();
            if (res.data.code == 1) {
              that.$vux.toast.show({
                text: res.data.msg,
                type: "success",
                position: "middle",
                time: 1200
              });
              that.srcs = [];
              let dom1 = document.getElementById("handcard1");
              dom1.value = "";
              that.data1="";
              that.src1="";
              
              let dom2 = document.getElementById("handcard2");
              dom2.value = "";
              that.data2="";
              that.src2="";

              let dom3 = document.getElementById("handcard3");
              dom3.value = "";
              that.data3="";
              that.src3="";

              let dom4 = document.getElementById("handcard4");
              dom4.value = "";
              that.data4="";
              that.src4="";

              that.detail="";
              let dom5 = document.getElementById("handcard5");
              dom5.value = "";
              that.data5="";
              that.src5="";
            } else {
              that.$vux.toast.show({
                text: res.data.msg,
                type: "cancel",
                position: "middle",
                time: 1200
              });
            }
          });
      } else {
        that.$vux.toast.show({
          text: that.$t('tip.full_tip'),
          type: "cancel",
          position: "middle",
          time: 1200
        });
      }
    }
  }
};



