
import $ from "jquery";
import XTable from 'vux/src/components/x-table/index.vue'


export default {
data() {
return {
list: []
};
},
components: {
XTable
},
mounted() {
let that = this;
mui.back = function() {
mui.plusReady(function() {
var main = plus.android.runtimeMainActivity();
main.moveTaskToBack(false);
});
error;
};
},
activated() {
let that = this;
that.getList();
window.t = setInterval(that.getList,3000);
},
methods: {
getList() {
let that = this;
that
.$http({
url: "/market/index",
method: "post",
data: {
token: that.$store.state.user_info.token
}
})
.then(function(res) {
if (res.data.code == 1) {
that.list = res.data.data;
that.$store.state.active_market = res.data.data[0];
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
},
toBs(item) {
this.$store.state.active_market = item;
this.$router.push({
name: "bsmarket"
});
}
}
};



