
import $ from "jquery";
import { PullRefresh } from "vant";
import XTable from 'vux/src/components/x-table/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'


export default {
data() {
return {
list: [],
page: 1,
load: true,
none: false,
lif: true,
nif: false,
loading: false,
isLoading: false
};
},
components: {
XTable,
LoadMore,
"van-pull-refresh": PullRefresh
},
mounted() {
let that = this;
mui.back = function() {
mui.plusReady(function() {
var main = plus.android.runtimeMainActivity();
main.moveTaskToBack(false);
});
error;
};
that.getlist(1);
},
methods: {
navTap(i) {
this.type = i;
let that = this;
that.loading = false;
that.nif = false;
that.page = 1;
that.list = [];
that.getlist(1);
},
toDetail(item) {
let id="";
if(item.trade_type_text_color=="green"){
id = item.id;
}else{
id = item.id;
}
this.$router.push({
name: "dealdetail",
params:{
id:id
}
});
},
onRefresh() {
let that = this;
that.isLoading = true;
that.loading = false;
that.nif = false;
that.page = 1;
that.list = [];
that.getlist(0);
},
loadMore() {
let that = this;
that.lif = true;
that.page++;
that.getlist();
},
getlist(i) {
let that = this;
if (i) {
that.lif = true;
}
that
.$http({
url: "/Transaction/order_list",
method: "post",
data: {
token: that.$store.state.user_info.token,
cur_id: that.$store.state.active_market.id,
page: that.page
}
})
.then(function(res) {
that.lif = false;
that.isLoading = false;
if (res.data.code == 1) {
if (res.data.data.length) {
that.list = that.list.concat(res.data.data);
console.log(that.list);
} else {
that.nif = true;
that.loading = true;
}
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
}
}
};



