
import $ from "jquery";
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

export default {
data() {
return {
show: false,
src: "",
name: "",
info: {},
menus: {
menu1: "拍照",
menu2: "从相册选择"
}
};
},
components: {
Actionsheet
},
mounted() {
let that = this;
mui.back = function() {
that.$router.back();
error;
};
},
activated() {
let that = this;
that
.$http({
url: "/user/user_head_info",
method: "post",
data: {
token: that.$store.state.user_info.token
}
})
.then(function(res) {
if (res.data.code == 1) {
that.info = res.data.data.user_info;
that.src = that.info.head_icon;
that.name = that.info.name;
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
},
methods: {
back() {
this.$router.back();
},
click(key) {
if (key == "menu1") {
this.getImage();
} else if(key == "menu2") {
this.galleryImgs();
}
},
pushImg1: function(e) {
let file = e.target,
reader = new FileReader(),
that = this,
_name,
_fileName;
_name = file.value;
_fileName = _name.substring(_name.lastIndexOf(".") + 1).toLowerCase();








reader.readAsDataURL(file.files[0]);
if (file.files[0].size > 10 * 1024 * 1024) {
that.$vux.toast.show({
text: that.$t("ups.d"),
type: "warn",
position: "middle",
time: 1500
});
} else {
that.$vux.loading.show({
text: that.$t("load.uping")
});
reader.onload = function() {
let result = this.result;



var image = new FormData();
image.append("file", e.target.files[0]);
image.append("type", "4");
image.append("token", that.$store.state.user_info.token);
that
.$http({
url: "/user/upload_pic",
method: "post",
data: image
})
.then(function(res) {
that.$vux.loading.hide();
if (res.data.code == 1) {
that.data1 = result;
that.src = res.data.url;
that
.$http({
url: "/user/change_user_info",
method: "post",
data: {
token: that.$store.state.user_info.token,
type: 1,
profile_icon: that.src,
nickname: ""
}
})
.then(function(res) {
if (res.data.code == 1) {
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "cancel",
position: "middle",
time: 1200
});
}
});
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "cancel",
position: "middle",
time: 1200
});
}
});
};
}


},

getImage() {
let that = this;
var cmr = plus.camera.getCamera();
cmr.captureImage(
function(p) {
plus.io.resolveLocalFileSystemURL(
p,
function(entry) {
that.compressImage(entry.toLocalURL(), entry.name);
},
function(e) {
plus.nativeUI.toast("读取拍照文件错误：" + e.message);
}
);
},
function(e) {},
{
filter: "image"
},
{
filename: "_doc/camera/",
index: 1
}
);
},

galleryImgs() {
let that = this;
plus.gallery.pick(
function(e) {
var name = e.substr(e.lastIndexOf("/") + 1);
that.compressImage(e, name);
},
function(e) {},
{
filter: "image"
},
{
filename: "_doc/camera/",
index: 1
}
);
},

compressImage(url, filename, divid) {
var name = "_doc/upload/" + divid + "-" + filename; 
plus.zip.compressImage(
{
src: url, //src: (String 类型 )压缩转换原始图片的路径
dst: name, //压缩转换目标图片的路径
quality: 20, //quality: (Number 类型 )压缩图片的质量.取值范围为1-100
overwrite: true 
},
function(event) {

var path = name; 


alert(path);
},
function(error) {
plus.nativeUI.toast("压缩图片失败，请稍候再试");
}
);
},
uName() {
this.$router.push({
name: "changename"
});
}
}
};



