import $ from "jquery";
import src from "../assets/logo.png";
export default {
  data() {
    return {
      way: 10,
      data1: "",
      urll: "",
      big_if: false,
      src: src,
      info: {}
    };
  },
  components: {},
  mounted() {
    let that = this;
    mui.back = function() {
      that.$router.push({
        name: "orderdetail",
        params: {
          type: 1
        }
      });
      error;
    };
    that
      .$http({
        url: "/Transaction/pay_info",
        method: "post",
        data: {
          token: that.$store.state.user_info.token,
          seller_id: that.$route.params.id,
          order_id: that.$route.params.id1
        }
      })
      .then(function(res) {
        if (res.data.code == 1) {
          that.info = res.data.data.list;
        } else {
          that.$vux.toast.show({
            text: res.data.msg,
            type: "cancel",
            position: "middle",
            time: 1200
          });
        }
      });
  },
  methods: {
    back() {
      this.$router.push({
        name: "orderdetail",
        params: {
          type: 1
        }
      });
    },
    // 上传
    pushImg1: function(e) {
      let file = e.target,
        reader = new FileReader(),
        that = this,
        _name,
        _fileName;
      _name = file.value;
      _fileName = _name.substring(_name.lastIndexOf(".") + 1).toLowerCase();
      // if (_fileName !== "png" && _fileName !== "jpg") {
      //   that.$vux.toast.show({
      //     text: "请上传图片类型文件！",
      //     type: "cancel",
      //     position: "middle",
      //     time: 1200
      //   });
      // }else{
      reader.readAsDataURL(file.files[0]);
      if (file.files[0].size > 10 * 1024 * 1024) {
        that.$vux.toast.show({
          text: that.$t("ups.d"),
          type: "warn",
          position: "middle",
          time: 1500
        });
      } else {
        that.$vux.loading.show({
          text: that.$t("load.uping")
        });
        reader.onload = function() {
          let result = this.result;

          // that.data1 = result;

          var image = new FormData();
          image.append("file", e.target.files[0]);
          image.append("type", "payimg");
          image.append("token", that.$store.state.user_info.token);
          that
            .$http({
              url: "/Transaction/upload_pic",
              method: "post",
              data: image
            })
            .then(function(res) {
              that.$vux.loading.hide();
              if (res.data.code == 1) {
                that.data1 = result;
                that.urll = res.data.data;
              } else {
                that.$vux.toast.show({
                  text: res.data.msg,
                  type: "cancel",
                  position: "middle",
                  time: 1200
                });
              }
            });
        };
      }

      // }
    },
    delImg1: function(e) {
      this.data1 = "";
      this.urll = "";
      let dom = document.getElementById("handcard");
      dom.value = "";
    },
    showImg() {
      this.big_if = true;
    },
    sub() {
      let that = this;
      if (that.way == 10) {
         that.$vux.toast.show({
            text: "请选择支付方式",
            type: "text",
            position: "middle",
            time: 1200
          });
      } else {
        that.$vux.loading.show({
          text: ""
        });
        that
          .$http({
            url: "/Transaction/do_pay",
            method: "post",
            data: {
              token: that.$store.state.user_info.token,
              order_id: that.$route.params.id1,
              payment_method: Number(that.way) + 1,
              voucher: that.urll
            }
          })
          .then(function(res) {
            that.$vux.loading.hide();
            if (res.data.code == 1) {
              that.$vux.alert.show({
                title: that.$t("topay.tip.t3"),
                content: that.$t("topay.tip.t2"),
                buttonText: that.$t("topay.tip.t4"),
                onShow() {},
                onHide() {
                  that.dialog0 = false;
                  that.$router.push({
                    name: "orderdetail",
                    params: {
                      type: 1
                    }
                  });
                }
              });
            } else {
              that.$vux.toast.show({
                text: res.data.msg,
                type: "cancel",
                position: "middle",
                time: 1200
              });
            }
          });
      }
    }
  }
};



