return {
usd:"0.00",
list:[]
};
},
components: {
XTable
},
mounted() {
let that = this;
mui.back = function() {
that.$router.push({
name: "mine"
});
error;
};
},
activated(){
let that = this;
that
.$http({
url: "/User/user_finance_list",
