that.list = res.data.data.list;
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
},
methods: {
back() {
this.$router.back();
},
toDetail(id){
this.$router.push({
name:"mainaccdetail",
params:{
id:id
}
})
},
}
};



