
import XInput from 'vux/src/components/x-input/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import $ from "jquery";
export default {
data() {
return {
lang: "",
lang_dlg: false,
types: ["password", "password", "password", "password"],
first: true,
second: false,
dialog0: false,
warn: "",
u_acc: "",
u_phone: "",
u_pwd: "",
u_pwd1: "",
uu_pwd: "",
uu_pwd1: "",
code: "",
yqm: "",
btn_msg: this.$t('code'),
ifAgree: false,
time: 60,
protocal: ""
};
},
components: {
XInput,
CheckIcon,
XDialog
},
mounted() {
let that = this;
mui.back = function() {
clearInterval(window.t);
that.$router.back();
error;
};
that.lang = that.$store.state.lang;
let height =
document.documentElement.clientHeight || document.body.clientHeight;
that.height = height;
$(".wrap").css("min-height", height);
if (that.$route.params.code != 0) {
that.yqm = that.$route.params.code;
}
that
.$http({
url: "/Phone/lang",
method: "post",
data: {
lang: that.$store.state.lang
}
})
.then(function(res) {});
},
methods: {
eyepwd(e) {
$(e.target)
.toggleClass("icon-yanjingbi")
.toggleClass("icon-yanjing");
if (
$(e.target)
.parents(".f_r")
.find("input")
.attr("type") == "password"
) {
$(e.target)
.parents(".f_r")
.find("input")
.attr("type", "text");
} else {
$(e.target)
.parents(".f_r")
.find("input")
.attr("type", "password");
}
},
changeLang(l) {
let that = this;
that.setLang(l);
that.lang_dlg = false;
that
.$http({
url: "/Phone/lang",
method: "post",
data: {
lang: that.$store.state.lang
}
})
.then(function(res) {});
},
focus(v, e) {
this.$store.commit("focus", { e: e });
},
blur(v, e) {
this.$store.commit("blur", { e: e });
},
focus1(e) {
this.$store.commit("focus", { e: e });
},
blur1(e) {
this.$store.commit("blur", { e: e });
},
back() {
let that = this;
clearInterval(window.t);
that.$router.back();
},
toProtocal() {
$(".mask").fadeIn();
},
hideProtocal() {
$(".mask").fadeOut();
},
getCode() {
let that = this;
let reg = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/;
let reg1 = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;
if (reg1.test(that.u_acc)) {
if (that.btn_msg == that.$t("code")) {
that.btn_msg = "60" + that.$t("second");
window.t = setInterval(function() {
that.time--;
that.btn_msg = that.time + that.$t("second");
if (that.time <= 0) {
that.btn_msg = that.$t("code");
clearInterval(window.t);
that.time = 60;
}
}, 1000);
that
.$http({
url: "/phone/email_code",
method: "post",
data: {
email: that.u_acc
}
})
.then(function(res) {
if (res.data.code == 1) {
that.$vux.toast.show({
text: res.data.msg,
type: "success",
position: "middle",
time: 1500
});
that.code = res.data.data;
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
that.btn_msg = that.$t("code");
clearInterval(window.t);
that.time = 60;
}
});
}
} else {
that.$vux.toast.show({
text: that.$t("tip.email_error"),
type: "cancel",
position: "middle",
time: 1200
});
}
},
next() {
let that = this;
let reg1 = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;
if (!reg1.test(that.u_acc)) {
that.$vux.toast.show({
text: that.$t("tip.email_error"),
type: "cancel",
position: "middle",
time: 1200
});
return false;
}
if (that.code == "") {
that.$vux.toast.show({
text: that.$t("tip.code_input"),
type: "cancel",
position: "middle",
time: 1200
});
return false;
}
that.first = false;
that.second = true;
},
sub() {
let that = this;
if (
that.u_acc &&
that.u_pwd &&
that.u_pwd1 &&
that.uu_pwd &&
that.uu_pwd1 &&
that.yqm
) {
if (that.u_pwd == that.u_pwd1) {
if (that.uu_pwd == that.uu_pwd1) {
that.regSuc();
} else {
that.$vux.toast.show({
text: that.$t("tip.trade_pwd_again"),
type: "cancel",
position: "middle",
time: 1200
});
}
} else {
that.$vux.toast.show({
text: that.$t("tip.login_pwd_again"),
type: "cancel",
position: "middle",
time: 1200
});
}
} else {
this.$vux.toast.show({
text: that.$t("tip.full_tip"),
type: "cancel",
position: "middle",
time: 1200
});
}
},
regSuc() {
let that = this;
that.$vux.loading.show({
text: ""
});
let ll = "";
if (that.$store.state.lang == "cn") {
ll = 1;
} else if (that.$store.state.lang == "ct") {
ll == 2;
} else {
ll = 3;
}
that
.$http({
url: "/phone/userReg",
method: "post",
data: {
email: that.u_acc,
invitation_code: that.yqm,
password: that.u_pwd,
repassword: that.u_pwd1,
payment_password: that.uu_pwd1,
repayment_password: that.uu_pwd1,
code: that.code,
language: ll
}
})
.then(function(res) {
that.dialog0 = false;
that.$vux.loading.hide();
if (res.data.code == 1) {
if (that.$route.params.code == 0) {
that.$vux.alert.show({
title: that.$t("suc"),
content: that.$t("confirm.c2"),
buttonText: that.$t("confirm.b1"),
onShow() {},
onHide() {
that.dialog0 = false;
that.$router.push({
name: "login"
});
}
});
} else {
that.$vux.toast.show({
text: "跳转下载链接",
type: "text",
position: "middle",
time: 1200
});
return false;
window.location.href = "";
}
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "cancel",
position: "middle",
time: 1200
});
}
});
}
}
};



