
import $ from "jquery";
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'


export default {
data() {
return {
dialog: false,
type: 0,
coin_info: {},
b_price: "",
b_num: "",
b_pwd: "",
s_price: "",
s_num: "",
s_pwd: "",
best_buy_price: "--",
best_sell_price: "--",
ratio: "",
ratio1: "",
u_usd: "",
u_coin: "",
max_sell_num: "",
tips: "",
min_price: 0,
max_price: 0
};
},
components: {
Tab,
TabItem,
XTable,
XDialog
},
computed: {
buyData() {
let that = this;
let cny = "";
let max = "";
let min = "";
let sum1 = "";
let sum2 = "";

if (that.b_price == "") {
cny = 0;
max = "--";
min = "--";








} else {
cny = (parseFloat(that.b_price) * parseFloat(that.ratio)).toFixed(2);
min = "1";
max = (parseFloat(that.max_price) / parseFloat(that.b_price)).toFixed(
2
);
}
if (that.b_price == "" || that.b_num == "") {
sum1 = "0.00";
sum2 = "0.00";

} else {
sum1 = (parseFloat(that.b_price) * parseFloat(that.b_num)).toFixed(2);
sum2 = (sum1 * parseFloat(that.ratio)).toFixed(2);

}
return {
cny: cny,
sum1: sum1,
sum2: sum2,
max: max,
min: min

};
},
sellData() {
let that = this;
let cny = "";
let sum1 = "";
let sum2 = "";
let fee = "";
if (that.s_price == "") {
cny = 0;
} else {
cny = (parseFloat(that.s_price) * parseFloat(that.ratio)).toFixed(2);
}
if (that.s_price == "" || that.s_num == "") {
sum1 = "0.00";
sum2 = "0.00";
fee = "0.00000";
} else {
sum1 = (parseFloat(that.s_price) * parseFloat(that.s_num)).toFixed(2);
sum2 = (sum1 * parseFloat(that.ratio)).toFixed(2);
fee = (parseFloat(that.s_num) * parseFloat(that.ratio1)).toFixed(5);
}
return {
cny: cny,
sum1: sum1,
sum2: sum2,
fee: fee
};
}
},
mounted() {
let that = this;
mui.back = function() {
mui.plusReady(function() {
var main = plus.android.runtimeMainActivity();
main.moveTaskToBack(false);
});
error;
};
this.$nextTick(() => {
if (this.$route.params.type) {
this.type = Number(this.$route.params.type);
} else {
this.type = 0;
}
});
that
.$http({
url: "http://btsq.qilinpz.com/api/Transaction/buy_range",
method: "post",
data: {
token: that.$store.state.user_info.token
}
})
.then(function(res) {
if (res.data.code == 1) {
that.min_price = res.data.data.min;
that.max_price = res.data.data.max;
}
});
that.getInfo();


that
.$http({
url: "/Transaction/get_user_finance",
method: "post",
data: {
token: that.$store.state.user_info.token,
cur_id: that.$store.state.active_market.id
}
})
.then(function(res) {
if (res.data.code == 1) {
that.ratio = res.data.data.usdt_cny;
that.ratio1 = res.data.data.service_charge;
that.u_usd = res.data.data.usdt;
that.u_coin = res.data.data.other;
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});

that
.$http({
url: "/Transaction/max_sell_num",
method: "post",
data: {
token: that.$store.state.user_info.token,
cur_id: that.$store.state.active_market.id
}
})
.then(function(res) {
if (res.data.code == 1) {
that.max_sell_num = parseFloat(res.data.data).toFixed(5);
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});

that
.$http({
url: "/Transaction/max_sell_tips",
method: "post",
data: {
token: that.$store.state.user_info.token
}
})
.then(function(res) {
if (res.data.code == 1) {
that.tips = res.data.data;
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
},
methods: {
navTap(i) {
let that = this;
this.type = i;
clearInterval(window.t);
this.getInfo();

},
toDetail() {
this.$router.push({
name: "coindetail"
});
},
num(e, i) {
clearInterval(window.t);
$(e.target).val(
$(e.target)
.val()
.match(/\d+\.?\d{0,5}/)
);
switch (i) {
case 1:
this.b_price = $(e.target).val();
break;
case 2:
this.b_num = $(e.target).val();
break;
case 3:
this.s_price = $(e.target).val();
break;
case 4:
this.s_num = $(e.target).val();
break;
}
},
percent(x) {
let that = this;
that.s_num = (parseFloat(that.max_sell_num) * parseFloat(x)).toFixed(5);
},
tip() {
this.dialog = true;
},
getInfo() {
let that = this;
let tt;
if (that.type == 0) {
tt = 2;
} else {
tt = 1;
}
that
.$http({
url: "/Transaction/get_info",
method: "post",
data: {
token: that.$store.state.user_info.token,
type: tt,
cur_id: that.$store.state.active_market.id
}
})
.then(function(res) {
if (res.data.code == 1) {
that.coin_info = res.data.data;
if (that.type == 0) {
that.best_buy_price = res.data.data.best_buy_num;
that.b_price = res.data.data.best_buy_num;
} else {
that.best_sell_price = res.data.data.best_sell_num;
that.s_price = res.data.data.best_sell_num;
}
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
},
buySub() {
let that = this;
if (that.b_price && that.b_num && that.b_pwd) {
that.$vux.loading.show({
text: ""
});
that
.$http({
url: "/Transaction/do_trade",
method: "post",
data: {
token: that.$store.state.user_info.token,
type: 2,
cur_id: that.$store.state.active_market.id,
price: that.b_price,
number: that.b_num,
pwd: that.b_pwd
}
})
.then(function(res) {
that.$vux.loading.hide();
if (res.data.code == 1) {
that.$vux.toast.show({
text: res.data.msg,
type: "success",
position: "middle",
time: 1200
});
that.b_price = "";
that.b_num = "";
that.b_pwd = "";
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "cancel",
position: "middle",
time: 1200
});
}
});
} else {
that.$vux.toast.show({
text: that.$t("tip.full_tip"),
type: "cancel",
position: "middle",
time: 1200
});
}
},
sellSub() {
let that = this;
if (that.s_price && that.s_num && that.s_pwd) {
that.$vux.loading.show({
text: ""
});
that
.$http({
url: "/Transaction/do_trade",
method: "post",
data: {
token: that.$store.state.user_info.token,
type: 1,
cur_id: that.$store.state.active_market.id,
price: that.s_price,
number: that.s_num,
pwd: that.s_pwd
}
})
.then(function(res) {
that.$vux.loading.hide();
if (res.data.code == 1) {
that.$vux.toast.show({
text: res.data.msg,
type: "success",
position: "middle",
time: 1200
});
that.max_sell_num = (parseFloat(that.max_sell_num) - parseFloat(that.s_num)).toFixed(5);
that.s_price = "";
that.s_num = "";
that.s_pwd = "";
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "cancel",
position: "middle",
time: 1200
});
}
});
} else {
that.$vux.toast.show({
text: that.$t("tip.full_tip"),
type: "cancel",
position: "middle",
time: 1200
});
}
}
}
};



