import $ from "jquery";
export default {
  data() {
    return {
      data: {},
      wallet: []
    };
  },
  components: {},
  mounted() {
    let that = this;
    mui.back = function() {
      mui.plusReady(function() {
        var main = plus.android.runtimeMainActivity();
        main.moveTaskToBack(false);
      });
      error;
    };
  },
  activated() {
    let that = this;
    that
      .$http({
        url: "/user/walletPage",
        method: "post",
        data: {
          token: that.$store.state.user_info.token
        }
      })
      .then(function(res) {
        if (res.data.code == 1) {
          that.data = res.data.data;
          that.wallet = res.data.data.currency;
        } else {
          that.$vux.toast.show({
            text: res.data.msg,
            type: "text",
            position: "middle",
            time: 1200
          });
        }
      });
  },
  methods: {
    toDetail(o, i) {
      this.$store.state.active_wallet = o;
      this.$router.push({
        name: "coindetail",
        params: {
          type: i
        }
      });
    }
  }
};



