export default {
  data() {
    return {
      data:"",
    };
  },
  components: {},
  mounted() {
    let that = this;
    mui.back = function() {
      that.$router.back();
      error;
    };
  },
  activated(){
    let that = this;
    that.data = {
      title:"",
      content:"",
    }
    that
      .$http({
        url: "/news/newsInfo",
        method: "post",
        data: {
          token:that.$store.state.user_info.token,
          id:that.$route.params.i
        }
      })
      .then(function(res) {
        if (res.data.code == 1) {
          that.data = res.data.data.info;
        } else {
          that.$vux.toast.show({
            text: res.data.msg,
            type: "text",
            position: "middle",
            time: 1200
          });
        }
      });
  },
  methods: {
    back() {
      let that = this;
      that.$router.back();
    }
  }
};



