    };
  },
  components: {
   
  },
  mounted() {
    let that = this;
    mui.back = function() {
      that.$router.push({
          name:"syssetting"
      });
      error;
    };
  
  },
  methods: {
    back() {
      this.$router.push({
          name:"syssetting"
      });
    },
    toDetail(i){
      this.$router.push({
        name:"bookdetail",
        params:{
          type:i
        }
      })
    },
    navTap(i) {
      if (i == 0) {
        this.$router.push({
          name: "message",
        });
      } else if (i == 1) {
        this.$router.push({
          name: "mymessage",
        });
      }
    }
  }
};



