
import $ from "jquery";
import { PullRefresh } from "vant";
import XTable from 'vux/src/components/x-table/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'


export default {
data() {
return {
list: [],
page: 1,
load: true,
none: false,
lif: true,
nif: false,
loading: false,
isLoading: false
};
},
components: {
XTable,
LoadMore,
"van-pull-refresh": PullRefresh
},
mounted() {
let that = this;
mui.back = function() {
that.$router.push({
name:"mine"
})
error;
};
that.getlist();
},
methods: {
navTap(i) {
this.type = i;
},
toDetail(id) {
this.$router.push({
name:"reply",
params:{
id:id
}
})
},
onRefresh() {
let that = this;
that.isLoading = true;
that.page = 1;
that.list = [];
that.getlist();
},
loadMore() {
let that = this;
that.lif = true;
that.page++;
that.getlist();
},
getlist() {
let that = this;
that
.$http({
url: "/user/get_feedback",
method: "post",
data: {
token: that.$store.state.user_info.token
}
})
.then(function(res) {
if (res.data.code == 1) {
if(res.data.data.length){
for(let v of res.data.data){
v.time1 = v.create_time.split(" ")[0];
v.time2 = v.create_time.split(" ")[1];
}
that.list = res.data.data;
}
}else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
}
}
};



