
import $ from "jquery";
import { PullRefresh } from "vant";
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'


export default {
data() {
return {
list: [],
page: 1,
load: true,
none: false,
lif: true,
nif: false,
loading: false,
isLoading: false,
type: 0,
number: "",
usdt: "",
cny: "",
icon: "",
ifPhone:"",
};
},
components: {
Tab,
TabItem,
LoadMore,
"van-pull-refresh": PullRefresh
},
mounted() {
let that = this;
mui.back = function() {
that.$router.back();
error;
};
},
computed:{
title(){
if(this.$route.params.type==0){
return this.$t('w1')
}else{
return this.$t('w2')
}
}
},
activated() {
let that = this;
that
.$http({
url: "/user/come_in_sec",
method: "post",
data: {
token: that.$store.state.user_info.token,
}
})
.then(function(res) {
if (res.data.code == 1) {
that.ifPhone = res.data.msg.phone;
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
this.$nextTick(() => {
this.type = 0;
this.page = 1;
this.lif = true;
this.nif = false;
this.loading = false;
this.getlist(1);
});
},
methods: {
back() {
this.$router.back();
},
navTap(i) {
this.list = [];
this.type = i;
this.page = 1;
this.lif = true;
this.nif = false;
this.loading = false;
this.getlist(1);
},
toDetail(id){
this.$router.push({
name:"transferdetail",
params:{
id:id
}
})
},
toTransfer() {
let that = this;
if(this.ifPhone==null){
this.$vux.toast.show({
text: that.$t('tip.phone_none'),
type: "cancel",
position: "middle",
time: 1200
});
return false;
}
this.$router.push({
name: "transfer"
});
},
toProceeds() {
let that = this;
if(this.ifPhone==null){
this.$vux.toast.show({
text: that.$t('tip.phone_none'),
type: "cancel",
position: "middle",
time: 1200
});
return false;
}
this.$router.push({
name: "proceeds"
});
},
onRefresh() {
let that = this;
that.isLoading = true;
that.loading = false;
that.nif = false;
that.page = 1;
that.list = [];
that.getlist(0);
},
loadMore() {
let that = this;
that.lif = true;
that.page++;
that.getlist(1);
},
getlist(i) {
let t = "";
if (this.type == 0) {
t = "";
} else if (this.type == 1||this.type==2) {
t = "1";
}else{
t="2"
}
let that = this;
if(i){
that.lif = true;
}
that
.$http({
url: "/wallet/curDetails",
method: "post",
data: {
token: that.$store.state.user_info.token,
p: that.page,
pase_size:20,
type: t,
id: 1,
wallet_type: that.$route.params.type+1,
}
})
.then(function(res) {
that.lif = false;
that.isLoading = false;
if (res.data.code == 1) {
that.number = res.data.data.number;
that.usdt = res.data.data.usdt;
that.cny = res.data.data.cny;
that.icon = res.data.data.icon;
if (res.data.data.list != "") {
for (let v of res.data.data.list) {
v.c = v.price.toString().substr(0, 1);
}
that.list = that.list.concat(res.data.data.list);
} else {
that.nif = true;
that.loading = true;
}
} else {
that.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "middle",
time: 1200
});
}
});
}
}
};



