

import Vue from 'vue'
import App from './App'
import router from './router'
import FastClick from 'fastclick'

FastClick.attach(document.body);

import 'lib-flexible/flexible'

import store from './store'

import ConfigPlugin from 'vux/src/plugins/config/index.js'





import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'

Vue.use(ConfirmPlugin)
Vue.use(ToastPlugin)
Vue.use(LoadingPlugin)
Vue.use(AlertPlugin)

import 'mint-ui/lib/style.css'
import {
InfiniteScroll
} from "mint-ui";
Vue.use(InfiniteScroll);

import VueClipboard from 'vue-clipboard2'
Vue.use(VueClipboard)
Vue.prototype.$http = instance;
import VueI18n from 'vue-i18n'
Vue.use(VueI18n)
import cn from "./base/lang/cn"
import ct from "./base/lang/ct"
import en from "./base/lang/en"
const i18n = new VueI18n({
locale: store.state.lang,
messages: {
'cn': cn,
'ct': ct,
'en': en,
}
});
Vue.$i18n = i18n;
Vue.prototype.setLang = function (l) {
this.$i18n.locale = l;
store.state.lang=l;
localStorage.setItem("lang",l);
};

import axios from 'axios'
import Qs from "qs"
axios.defaults.transformRequest = [function(data) {
if(data.toString()=="[object FormData]"){
return data;
}else{
return Qs.stringify(data)
}
}];
const host = process.env.NODE_ENV === 'development' ? '/' : 'http://btsq.qilinpz.com/api/' 
const instance = axios.create({
baseURL: host,
timeout: 10000,
withCredentials: true,
})

instance.interceptors.response.use(function (response) {

if(response.data.code==0){

}
return response;
}, function (error) {

Vue.$vux.loading.hide();
Vue.$vux.toast.show({
text: "该账号在别处登录，请重新登录！",
type: "cancel",
position: "middle",
time: 1200
});
return Promise.reject(error);
});
Vue.prototype.$http = instance;




Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
el: '#app',
router,
store,
i18n,
components: {
App
},
template: '<App/>'
})



