export default {
  data() {
    return {
      data: "",
      link: ""
    };
  },
  components: {},
  mounted() {
    let that = this;
    mui.back = function() {
      that.$router.back();
      error;
    };
  },
  computed: {
    title() {
      if (this.$route.params.type == 2) {
        return "BAUDCOIN（波特币）白皮书";
      } else {
        return "BAUDCOIN white-book";
      }
    }
  },
  activated() {
    let that = this;
    that
      .$http({
        url: "/Phone/bps",
        method: "post",
        data: {
          type: that.$route.params.type
        }
      })
      .then(function(res) {
        if (res.data.code == 1) {
          that.link = res.data.data;
        } else {
          that.$vux.toast.show({
            text: res.data.msg,
            type: "text",
            position: "middle",
            time: 1200
          });
        }
      });
  },
  methods: {
    back() {
      let that = this;
      that.$router.back();
    }
  }
};



